/*------------------------------------*\
	$FORMS
\*------------------------------------*/

form ol, form ul {
	margin-left: 0;
}

fieldset {
	border: 0;
	padding: 0;
	margin: 0;
}

legend {
	display: inline-block;
}

legend.h2 {
	// Webkit doesn't render margin-top on legend
	padding-top: 1em;
	margin-top: 0;
}

label {
	display: block;
	padding-bottom: $quarterSpace;
}

button, input, select, textarea {
	font-family: inherit;
	@include fontSizeAlways(100%);
	margin: 0;
}

input, textarea {
	width: 100%;
	border: 0;
	padding: $pad-half 0.65rem;
	@include lineHeight(formField);
}

textarea {
	height: 7rem;
	resize: vertical;
}

input[type='text'], input[type='email'], input[type='search'], input[type='url'], input[type='number'], textarea {
	appearance: none;
	color: getColor(grijs darkest);

	&::-webkit-input-placeholder { color: getColor(grijs grijs7); }
	&::-moz-placeholder { color: getColor(grijs grijs7); }
	&:-ms-input-placeholder { color: getColor(grijs grijs7); }
}

button, input[type='submit'] {
	padding: $pad-half;
	background: getColor(grijs darkest);
	border: 1px solid getColor(grijs default);
	cursor: pointer;
	color: getColor(wit);
}

input[type='checkbox'],
input[type='radio'] {
	width: auto;
	margin-right: 0.3em;
}

input[type='search'] {
	-webkit-appearance: none;
	border-radius: 0;
}

input[type='search']::-webkit-search-decoration {
	-webkit-appearance: none;
}

input[type='search']::-webkit-search-cancel-button {
	@include fontSizeAlways(200%);
	cursor: pointer;
}

input[type='search']::-ms-clear {
	display: inline-block;
	@include fontSizeAlways(200%);
	cursor: pointer;
}

select {
	width: 100%;
	padding: $pad-half 0.65rem;
	-webkit-appearance: none;
	appearance: none;
	border: 0;
	color: getColor(grijs darkest);
	@include lineHeight(formField);

	&::-ms-expand {
		display: none;
	}
}

//Form Field Container
.field-container {
	margin-bottom: $space;
}

.inline-form {

	fieldset, .inline-container {
		position: relative;
	}

	input[type='submit'], button, .btn {
		padding: 0.65rem 1.3rem;
		background: getColor(grijs darkest);
		position: absolute;
		top: 0;
		right: 0;
		z-index: 1;
		width: auto;

		&:hover, &:focus {
			background: getColor(grijs default);
			color: getColor(wit);
		}
	}
}

// PK to clean this up
.inline-search-results-form {
	width: 100%;

	fieldset, .inline-container {
		position: relative;
	}

	input[type='submit'], button {
		position: absolute;
		top: 0;
		right: 0;
		z-index: 1;
		width: auto;
	}
}

/* Validation */
.has-error {
	border-color: getColor(violet default);
}

.is-valid {
	border-color: getColor(mosgroen default);
}

.inputhpfield {
	display: none;
}

.formField-input--error {
	background-color: getColor(violet lightest);
}

.inputErrorMessage {
	position: absolute;
	display: block;
	left: 0;
	top: 100%;
	margin-top: -1px;
	background-color: getColor(violet default);
	color: getColor(wit);
	@include fontSize(small, $lineHeight: formField);
	padding: 0.2rem 0.4rem;
	@include zIndex(overlay);
}


/*------------------------------------*\
	$SPECIFIC FORMS
\*------------------------------------*/

/* Search Form */
.search-field {
	padding-right: 3em;
}

.inline-form .search-submit {
	background: none;
	padding: 0.78em 1em;
	border: 0;
	border-left: 1px solid getColor(grijs default);
	color: getColor(grijs default);
}


/* Form Buttons */
.formButton {
	&-cancel {
		float: left;
	}
	&-submit {
		float: right;
	}
}

.feedback-form {
	padding: 1rem;
}

.feedback-radio .formField-optionWrapper {
	background: getColor(hemelblauw light);
	padding: {
		left: 1rem;
		right: 1rem;
	}
	margin-right: 1rem;
	color: getColor(hemelblauw darkest);
	font-weight: bold;
}

.formField-row {
	margin: {
		top: 0;
		bottom: 1.2rem;
	};
	@include mqLesserThan(smallMedium) {
		margin-bottom: 1.6rem;
	}

}

.fieldset {
	margin-bottom: 1rem;
}

.formField {
	$breakpoint: smallMedium;

	@include mqGreaterThan($breakpoint) {
		display: flex;
		margin: {
			left: negative($halfSpace);
			right: negative($halfSpace);
		};

		&--fieldset {
			display: block;
		}
	}

	&-label {
		display: block;
		margin: 0.2em 0;
		color: getColor(hemelblauw darkest);
		@include mqGreaterThan($breakpoint) {
			text-align: right;
			width: calcColumnWidth(1/3);
			margin: {
				left: $halfSpace;
				right: $halfSpace;
			}

			&--legend {
				float: left;
			}
		}

	}

	&-wrapper {
		display: block;
		position: relative;

		@include mqGreaterThan($breakpoint) {
			width: calcColumnWidth(2/3);
			margin: {
				left: $halfSpace;
				right: $halfSpace;
			}
		}

		@include mqGreaterThan(large) {
			width: calcColumnWidth(1/2);
		}

		.interfaceLink-text {
			margin-right: 0;
		}
	}

	&-radioWrapper {
		position: relative;

		@include mqGreaterThan($breakpoint) {
			float: left;
			width: calcColumnWidth(2/3);
			margin: {
				left: $halfSpace;
				right: $halfSpace;
			}
		}

	}

	&-radioInputWrapper {
		position: relative;
		display: flex;
		flex-wrap: wrap;
	}

	&-selectWrapper {
		position: relative;
		display: block;
		width: 100%;
	}

	&-optionWrapper {
		display: flex;
		margin-right: 2rem;
		padding: 0;
	}

	&-option {
		//For specificity
		&#{&} {
			width: 0.85rem;
			height: 0.85rem;
			flex: 0 0 auto;
			margin-top: auto;
			margin-bottom: auto;
		}
	}

	&-optionLabel {
		position: relative;
		top: 1px;
	}

	&-inputWrapper {
		display: flex;
		position: relative;
		background-color: getColor(grijs lightest);
		border: 1px solid getColor(hemelblauw darkest);

		.select-icon-down {
			width: 1.5em;
			height: 1.5em;
			position: absolute;
			top: 0.4rem;
			right: 0.4rem;
			pointer-events: none;
			background-color: getColor(grijs lightest);
		}
	}

	&-input {
		background: none;
		flex: 1 1 auto;
		width: 100%;
		border: 0;
		padding: 0.5rem 0.65rem;
		color: getColor(grijs darkest);
		@include lineHeight(formField);
	}

	&-optional {
		background: none;
		flex: 0 0 auto;
		color: getColor(hemelblauw darkest);
		font-style: italic;
		padding: 0.5rem 0.9rem;
		@include lineHeight(formField);

		&--select {
			// padding-left: 0.1rem;
			border-left: 1px solid getColor(hemelblauw darkest);
		}
	}

	&-description {
		display: block;
		margin-top: 0.3rem;
		font-style: italic;
	}
}