$loadingIndicatorBars: (
	getColor(violet default),
	getColor(mosgroen default),
	getColor(hemelblauw default),
) !default;

$loadingIndicatorDuration: 3s !default;
$loadingIndicatorBarCount: length($loadingIndicatorBars);

.loadingIndicator {
	@include zIndex(dialog);
	position: fixed;
	overflow: hidden;
	left: 0;
	right: 0;
	top: 0;
	height: 0.3rem;
}

.loadingIndicator-text {
	@include accHide;
}

.loadingIndicator-bars {
	height: 100%;
	position: relative;
	width: 100%;
	background-color: nth($loadingIndicatorBars, $loadingIndicatorBarCount);
}

.loadingIndicator-bar {
	@function getDelay($duration, $barCount, $i) {
		@return (($i - 1) * $duration / $barCount);
	}

	display: block;
	position: absolute;
	width: 0;
	height: 100%;
	left: 50%;
	text-align: center;

	@for $i from 1 through $loadingIndicatorBarCount {
		&:nth-child(#{$i}) {
			$loadingIndicatorDelay: getDelay($loadingIndicatorDuration, $loadingIndicatorBarCount, $i);
			@include loading($loadingIndicatorDuration, $loadingIndicatorDelay, $loadingIndicatorBarCount);
			background-color: nth($loadingIndicatorBars, $i);
		}
	}
}


