$lineBlockBorderColor: getColor(hemelblauw darkest) !default;

.lineBlock {
	padding-top: 0.75rem;
	border-top-width: 3px;
	border-top-style: solid;

	border-top-color: $lineBlockBorderColor;

	@include theme((border-top-color: foreground), '.lineBlock', true);

	.h2, .h3, .h4 {
		margin-top: 0;
	}
}