.enquete-inner {
	padding: 0.5rem 0 0.5rem 0.25rem;

	@include mqGreaterThan(medium) {
		padding: 1rem 0 1rem 1.5rem;
	}

	.formField,
	.formField-wrapper,
	.formField-radioWrapper {
		margin-left: 0;
		margin-right: 0;
	}

	.formField-radioWrapper {
		width: 100%;
		float: none;
	}

	.formField-optionWrapper {
		width: 100%;

		+ input {
			margin-top: 0.5rem;
		}
	}

	.formField-label {
		padding-left: 0;
		padding-right: 0;
	}

	.formField-description {
		margin-top: 1.5rem;
	}
}

.enquete-title {
	margin-bottom: 0.75rem;
	display: block;
	@include fontSizeAlways(1.1em);
}

.previous-button {
	flex: 1 1 auto;
	fill: white;
	@include lineHeight(heading);
}
