.onderwerpHeader {

	&-titleContainer {
		@include insideMargin(1.8rem 1rem);
		background: getColor(hemelblauw lighter);
	}

	&-tabContainer {
		position: relative;

		background: getColor(hemelblauw lighter);
	}
	//Artikelpagina in een onderwerppagina
	+ .main {
		margin-top: 1rem;
		@include mqLesserThan(mediumLarge) {
			margin-top: 2rem;
		}
	}

}

.onderwerpHeading {
	color: getColor(hemelblauw darkest);
	margin-bottom: 1.6rem;
}