// Wordt nu gebruikt voor: Toptaken

$labelBackgroundColor: getColor(wit) !default;
$labelTextColor:  getColor(hemelblauw grijscontrast) !default;
$labelTextHoverColor: getColor(hemelblauw darkest) !default;

.labelList {
	@include noBullets;
	margin: 1rem 0 1.5rem 0;
	display: flex;
	flex-wrap: wrap;
	align-items: flex-start;

	.homepageHeader & {
		margin-bottom: 0;
	}

	&-title {
		margin-top: 1rem;
		font-weight: bold;
	}

	&-item {
		margin: 0 0.5rem 0.5rem 0;
		padding: 0;
		display: inline-block;
	}

	&-link {
		@include noUnderline;
		padding: 0.2rem 0.7rem 0.3rem 1rem;
		display: inherit;
		@include lineHeight(root);
		background-color: $labelBackgroundColor;
		color: $labelTextColor;
		fill: $labelTextColor;
		transition-property: color, transform;
		transition-duration: 0.2s;
		transition-timing-function: ease-in-out;

		@include hoverEtc {
			color: $labelTextHoverColor;
			fill: $labelTextHoverColor;

			.labelList-text {
				border-bottom: 1px solid;
			}
		}
	}
	&-text {
		flex: 1 1 0;
		@include lineHeight(labelListItem);
	}
}

.noDropDown { //TopTaken op homepage
	margin-top: 5rem;
}