.logo {
	display: block;
	position: relative;

	@include mqGreaterThan(mediumLarge) {
		width: calcColumnWidth(2/3);
	}
}

.logo-link {
	@include noUnderline;

	font-weight: bold;
	color: getColor(grijs darkest);
	width: fit-content;
	display: block;
	&:focus {
		outline-width: 1px;
		outline-style: dotted;
	}
}

.logo-image {
	width: auto;
	display: block;

	$logoHeight: 2.6rem;

	height: $logoHeight;
	width: ($logoHeight * 3);

	@include mqGreaterThan(small) {
		$logoHeight: 3rem;

		height: $logoHeight;
		width: ($logoHeight * 3);
	}
	@include mqGreaterThan(large) {
		$logoHeight: 3.12rem;

		height: $logoHeight;
		width: ($logoHeight * 3);
	}
}