.menuBar-context {
	margin-top: auto;
	transform: translateY($menuBarWidthSmall);
	transition: transform 0.2s;

	@include mqGreaterThan(small) {
		transform: translateY($menuBarWidthLarge);
		// less top-/bottom-margin on landscape and zoomed-in screens
		@include mqLesserThan(smallHeight) {
			transform: translateY(2rem);
		}
	}
}

.menuBar-context--showBackToTop {
	transform: none;
}