.is-menuOpened {
	overflow: hidden;
}

.container {
	width: 100%;
	
	@include mqGreaterThan($cardBreakpoint) {
		min-height: 100vh;
	}
}

.menuBar {
	position: relative;
	height: 100%;
	width: $menuBarWidthSmall;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	@include zIndex(menuBar bar);

	@include mqGreaterThan(small) {
		width: $menuBarWidthLarge;
		padding: {
			top: (1rem/17);
			bottom: (1rem/17);
		}
	}

	@include mqGreaterThan(large) {
		width: $menuBarWidthExtraLarge;
		padding: {
			top: (1rem/18);
			bottom: (1rem/18);
		}
	}

	@include mqGreaterThan(extraExtraExtraLarge) {
		width: $menuBarWidthExtraExtraLarge;
	}

	@include before('') {
		position: absolute;
		left: 0;
		top: 0;
		bottom: 0;
		width: 100%;
		transition: width 0.4s;
		background: $menuBarBg;
		background: linear-gradient(rgba($menuBarBgStop1, $menuBarOpacity), rgba($menuBarBgStop2, $menuBarOpacity));
	}

	@include after('') {
		position: absolute;
		display: block;
		background: getColor(hemelblauw lighter);
		top: 0;
		bottom: 0;
		right: 100%;
		width: 2000px;
		width: 100vw;
		@include mqGreaterThan(extraExtraExtraLarge) {
			background: getColor(grijs lightest);
		}
	}
}

.menuBar--title {
	@extend .h2;
}

.menu-backgroundClose {
	position: fixed;
	display: block;
	left: 0;
	right: 0;
	bottom: 0;
	top: 0;
	z-index: getZIndex(overlay);
	@include cleanButton;
	@include hideText;
	background-color: rgba(getColor(hemelblauw darkest), 0.6);
	width: 100%;
	cursor: default;
}

.is-menuBar-retracted { // todo ongebruikt?
	@include mqLesserThan(small) {
		background: rgba(getColor(wit), 0.9);
		box-shadow: inset -1px 0 0 0 rgba(getColor(grijs light), 1);

		@include before('') {
			width: 0.25rem;
		}
		.menuBar-icon {
			fill: change-color(getColor(hemelblauw light), $lightness: 48%);
		}
	}
}