.popup {
	padding: {
		top: $halfSpace;
		bottom: $halfSpace;
		left: $space;
		right: $space;
	}
	position: relative;

	@include mqGreaterThan($cardBreakpoint) {

		border: 1px solid;
		background: getColor(wit);

		@include theme((border-color: foreground), #{&}, true);
	}

	.close--absoluteRight + &-content {
		margin-top: 1rem;
	}
}

.popup-topBar {
	margin-bottom: 0.5rem;
	padding-right: 2rem;

	@include mqGreaterThan(small) {
		padding-right: 3rem;
	}
}

.popup-title {
	margin: 0;
	@include theme((color: foreground), #{&}, true);
}

.popup-moreLink {
	margin-top: 1rem;

	.underliner {
		color: getColor(hemelblauw default);
	}
}
