$menuBarOverlayTextColor: getColor(wit) !default;
$menuBarOverlayIconBgColor: getColor(wit) !default;

.menuBar-overlayContent {
	position: absolute;
	bottom: 0;
	left: 100%;
	background: rgba(getColor(hemelblauw default), 0.95);
	color: $menuBarOverlayTextColor;
	fill: $menuBarOverlayIconBgColor;
	width: 25rem;
	max-height: calc(100vh - 2rem);
	transform-origin: 0 100%;
	transform-origin: 0 calc(100% - 1.58rem);
	min-height: $menuBarWidthSmall;

	@include zIndex(menuBar contextContent);
	max-width: calc(100vw - (#{$menuBarWidthSmall} + 1rem));

	@include mqGreaterThan(small) {
		max-width: calc(100vw - #{$menuBarWidthLarge});
		min-height: $menuBarWidthLarge;
	}
	@include mqGreaterThan(large) {
		max-width: calc(100vw - #{$menuBarWidthExtraLarge});
	}

	@include mqGreaterThan(extraExtraExtraLarge) {
		max-width: calc(100vw - #{$menuBarWidthExtraExtraLarge});
	}

	.formField {
		display: block;
	}

	.formField-label {
		color: getColor(wit);
		text-align: left;
		width: 100%;
		margin: 0;
		padding-left: $halfSpace;
		padding-right: 3.5rem;
		padding-bottom: 0.5rem;

		@include mqLesserThan(smallMedium) {
			padding-left: 0;
			margin-bottom: 0.5rem;
		}
	}

	input {
		color: getColor(grijs darkest);
	}
}

.menuBar-overlayContent,
.menuBar-overlayContent-scroll {
	@include mqLesserThan(smallHeight) {
		max-height: calc(100vh - 3rem);
	}
	@include mqBetween(smallHeight mediumHeight) {
		max-height: calc(100vh - 5rem);
	}
	@include mqBetween(mediumHeight largeHeight) {
		max-height: calc(100vh - 6rem);
	}
}

.menuBar-overlayContent-scroll {
	padding: 0.55rem 1rem;
	overflow-y: auto;
}

.menuBar-overlayContent-wrapper {
	position: relative;
}