.content-block {
	@extend .block;
	@extend .media;
	@extend .media--alignTop;
	background-color: #e5f0f6;
	padding: 1rem;

	h2, .h2,
	h3, .h3,
	h4, .h4 {
		margin-top: 0;
	}
}

.content-block--regelhulp {
	padding-top: 1.5rem;
	padding-left: 0;
	background-color: getColor(wit);
	border-top: 3px solid getColor(violet default);
}

.content-block--regelhulp h2,
.content-block--regelhulp h3 {
	color: getColor(violet default);
}

.content-block--regelhulp .content-block__icon-wrapper {
	width: 3rem;
	height: 3rem;
	padding: 0.5rem;
	background-color: getColor(violet default);
	border-radius: 100%;
}

.content-block--regelhulp .content-block__icon {
	fill: getColor(wit);
}

.content-block--regelhulp .content-block__content {
	padding-top: 0.5rem;

	a {
		color: getColor(violet default);
	}
}

.content-block__icon-wrapper {
	@extend .media-figure;
	width: 2.5rem;
	margin-top: -10px;
}

.content-block__icon {
	fill: getColor(hemelblauw default);
}

.content-block__content {
	@extend .media-text;

	.content-block__icon-wrapper + & {
		padding-left: 1rem;
	}
}