.menuBar-button {
	width: $menuBarWidthSmall;
	height: $menuBarWidthSmall;
	margin-top: 1rem;
	margin-bottom: 1rem;
	padding: ($menuIconSizePaddingRatioSmall * $menuBarWidthSmall);
	display: block;
	position: relative;
	left: 0;
	overflow: hidden;
	background: none;
	border: none;
	color: $menuBarButtonColor;
	transition: background-color, color 200ms ease-in-out;

	@include mqGreaterThan(small) {
		width: $menuBarWidthLarge;
		height: $menuBarWidthLarge;
		padding: ($menuIconSizePaddingRatioLarge * $menuBarWidthLarge);
		margin-top: (10rem/17);
		margin-bottom: (10rem/17);
	}

	@include mqGreaterThan(large) {
		width: $menuBarWidthExtraLarge;
		height: $menuBarWidthLarge;
		margin-top: (12rem/18);
		margin-bottom: (12rem/18);
	}

	@include mqGreaterThan(extraExtraExtraLarge) {
		width: $menuBarWidthExtraExtraLarge;
		margin-top: 0.9rem;
		margin-bottom: 0.9rem;
	}

	@include hoverEtc {
		background-color: $menuBarButtonHoverColor;
		color: inherit;

		.menuBar-icon {
			fill: getColor(zwart);
		}
	}

	@media (pointer: coarse) {
		&:hover {
			background-color: $menuBarBg;
		}
	}

	&.is-opened {
		background-color: $menuBarButtonIsOpenedColor;
		color: getColor(zwart);

		.menuBar-icon {
			fill: getColor(zwart);
			transform: scale(1.1);
		}
	}

	// less top-/bottom-margin on landscape and zoomed-in screens
	@include mqLesserThan(smallHeight) {
		margin: {
			top: 0.225rem;
			bottom: 0.225rem;
		}
	}
	@include mqLesserThan(smallHeight) {
		@include mqBetween(extraSmall extraLarge) {
			height: $menuBarWidthSmall;
			padding: {
				top: ($menuIconSizePaddingRatioSmall * $menuBarWidthSmall);
				bottom: ($menuIconSizePaddingRatioSmall * $menuBarWidthSmall);
			}
		}
	}
	@include mqLesserThan(smallHeight) {
		@include mqGreaterThan(large) {
			height: $menuBarWidthSmall;
			padding: {
				top: ($menuIconSizePaddingRatioSmall * $menuBarWidthSmall);
				bottom: ($menuIconSizePaddingRatioSmall * $menuBarWidthSmall);
			}
		}
	}
}

.menuBar-button--context {
	&.is-opened {
		background-color: getColor(hemelblauw default);
		color: getColor(wit);

		.menuBar-icon {
			fill: getColor(wit);
		}
	}
}

.menuBar-button--toTop {
	background-color: rgba(getColor(hemelblauw lighter), 0.5);
	margin-bottom: 0;
}

.menuBar-button-inner {
	margin: 0 auto; // needed for mobile Safari when .menuBar-button is a <button> element
	
	@include mqGreaterThan(large) {
		display: flex;
		flex-direction: row;
		align-items: center;
	}
}

.menuBar-button-text {
	position: relative;

	@include mqLesserThan(large) {
		@include accHide;
	}
	@include mqGreaterThan(large) {
		text-align: right;
		@include fontSize(menuLabels);
		order: 1;
		flex: 1 1 auto;
		padding-right: ($menuIconSizePaddingRatioLarge * $menuBarWidthLarge);

	}
	@include mqGreaterThan(extraExtraLarge) {
		padding-right: $pad-half;
	}
}