$payOffFlex: 0 0 auto !default;
$payOffTextAlign: left !default;
$payOffTextColor: getColor(grijs grijs6) !default;
$payOffPaddingLeft: 0.5rem !default;
$payOffMarginLeft: auto !default;

.payOff {
	padding-left: $payOffPaddingLeft;
	flex: $payOffFlex;
	margin-left: $payOffMarginLeft;
	align-self: center;

	@include mqGreaterThan(mediumLarge) {
		width: calcColumnWidth(1/3);
	}
}

.payOff-text {
	margin: 0 0 0.1rem 0;
	text-align: $payOffTextAlign;
	color: $payOffTextColor;
	font-style: italic;
	line-height: 1.2;

	@include mqLesserThan(small) {
		padding-right: 0.5rem;
		font-size: 0.75rem;
	}
}

.payOff-text--home {
	color: getColor(zwart);
}