.cookieAlert {
	width: 100%;
	position: fixed;
	left: 0;
	bottom: 0;
	@include zIndex(dialog);

	&-container {
		max-width: map-get($maxPageWidth, rem);
		padding: {
			top: 1rem;
			bottom: 1rem;
		}
		display: flex;
		position: relative;
		background: rgba(getColor(hemelblauw darkest), 0.9);
		color: getColor(wit);

		@include mqGreaterThan(largest) {
			left: 50%;
			margin-left: negative(map-get($maxPageWidth, rem) / 2);
			max-width: map-get($maxPageWidth, rem);
		}
	}

	.contentContainer {

		@include mqGreaterThan(medium) {
			width: 100%; // IE11
			display: flex;
		}
	}

	&-buttonContainer {
		margin-top: 1rem;
		margin-bottom: 0.5rem;
		flex: 1;
		text-align: center;

		@include mqGreaterThan(medium) {
			margin-top: 0;
		}
	}

	&-button {
		background: getColor(mosgroen default);
		@include hoverEtc {
			background: getColor(mosgroen darker);
		}
	}

	&-text {
		margin: 0;
		padding-right: 3rem;
		flex: 2;

		@include mqGreaterThan(medium) {
			margin-right: 0;
		}
	}

	&-readMore {

		@include hoverEtc {
			border-bottom: 1px solid;
			color: white;
		}
	}

	&-close {
		right: 1rem;

		@include mqGreaterThan(large) {
			right: 2rem;
		}
	}
}