$overViewTitleOpacity: 0.8;
$overViewTitleBlue: getColor(hemelblauw darkest);
$overViewTitleViolet: getColor(violet default);
$overViewTitlePurple: getColor(paars default);
$overViewTitleGreen: getColor(mosgroen default);
$overViewTitleGrey: getColor(grijs default);

.overviewPageTitle {
	background: getColor(hemelblauw default);
	@include theme((background: foreground), #{&}, true);
}

.overviewPageTitle-text {
	color: getColor(wit);

	margin: {
		top: 0.4rem;
		bottom: 0.3rem;
	}

	@include mqGreaterThan(extraExtraExtraLarge) {
		margin: {
			top: 0.6rem;
			bottom: 0.5rem;
		}
	}
}

.overviewPageTitle--transparent {
	&.overviewPageTitle--blue {
		background-color: rgba($overViewTitleBlue, $overViewTitleOpacity);
	}
	&.overviewPageTitle--violet {
		background-color: rgba($overViewTitleViolet, $overViewTitleOpacity);
	}
	&.overviewPageTitle--purple {
		background-color: rgba($overViewTitlePurple, $overViewTitleOpacity);
	}
	&.overviewPageTitle--green {
		background-color: rgba($overViewTitleGreen, $overViewTitleOpacity);
	}
	&.overviewPageTitle--grey {
		background-color: rgba($overViewTitleGrey, $overViewTitleOpacity);
	}
}

.pageTitle {
	display: flex;
	align-items: flex-start;
}

.pageTitle-mobile {
	background: getColor(paars lightest);
	color: getColor(paars default);
	margin: 0;
	padding: {
		left: 1rem;
		right: 1rem;
		bottom: 0.5rem;
	}
}

.pageTitle-mobile-title {
	flex-grow: 1;
	padding-top: 0.5rem;
}