.heroContainer,
.heroForeground {
	position: relative;
	min-height: 30vw;
	@include mqGreaterThan(mediumLarge) {
		min-height: 20vw;
	}
	@include mqGreaterThan(extraLarge) {
		min-height: 15vw;
	}
}

.heroContainer--withAnchorBlock {
	margin-bottom: -2.4rem;
	padding-bottom: 2.4rem;
}

.heroImage {
	@include theme((background: background), #{&}, true);
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	overflow: hidden;
}

.heroImage-image {
	width: 100%;
	display: block;
	@supports(object-fit: cover) {
		object-fit: cover;
		height: 100%;
	}
}