.headerStory {
	width: 15rem;
	margin: 0;
	padding: 0.6rem 1.5rem;
	display: block;
	position: absolute;
	right: 1rem;
	bottom: 1rem;
	background: rgba(getColor(wit), 0.75);
	@include noUnderline;
	@include lineHeight(heading);
	transform-origin: 100% 100%;
	transition: transform 200ms;
	text-align: center;

	&:before {
		width: 0;
		height: 0;
		position: absolute;
		left: -1.1rem;
		content: '';
		border-top: 10px solid transparent;
		border-bottom: 10px solid transparent;
		border-right: 22px solid rgba(getColor(wit), 0.75);
	}

	@include mqLesserThan(mediumLarge) {
		width: 13.1rem;
		bottom: 0.4rem;
		right: 1.6rem;
		@include lineHeight(textSize);

		&:before {
			border-left: 10px solid transparent;
			border-right: 10px solid transparent;
			border-bottom: 22px solid rgba(getColor(wit), 0.75);
			left: 1.8rem;
			top: -1.9rem;

		}
	}

	@include mqLesserThan(smallMedium) {
		display: none;
	}

	&-intro {
		margin: 0;
		font-weight: normal;
		font-style: italic;
		@include fontSize(small);
		color: getColor(zwart);
	}
	&-blockquote {
		margin-bottom: 0.8rem;
		padding: 0;
		color: getColor(zwart);
		border: none;
		font-weight: bold;
	}

	&-button {
		@include cleanButton;
		padding: 9px;
		text-transform: none;
		background-color: getColor(violet default);
		color: getColor(wit);
		@include fontSize(extrasmall);
		@include mqLesserThan(mediumLarge) {
			text-align: center;
		}

		@include hoverEtc {
			background-color: darken(getColor(violet default), 5%);
			color: getColor(wit);
		}
	}

	&-citation {
		margin-bottom: 0.5rem;
		font-style: italic;
		@include fontSize(small);
	}
}