// FONTSIZES

// Overview of all fontsizes
// submaps are used for responsive sizes
$fontSize: (
	root: 1rem,
	halfRoot: 0.5rem,
	small: 0.875rem,
	extrasmall: 0.75rem,
	menuLabelsExtraSmall: 0.45rem,
	menuLabelsSmall: 0.65rem,
	menuLabelsMedium: 0.68rem,
	menuLabels: 0.9rem,
	h1: (
		all: 1.7rem,
		small: 1.9rem,
		large: 2.2rem,
	),
	h2: (
		all: 1.3rem,
		small: 1.4rem,
		large: 1.5rem,
	),
	h3: (
		all: 1.1rem,
	),
	h4: (
		all: 1rem,
	),
	brandBarPageTitle: 1.75rem,
	introText: 1.2rem,
	payOff: 1.1rem,
);

// Example:
// font-size: getFontSize(h1 small);
@function getFontSize ($name) {
	@return mapDeepGet($fontSize, $name);
}
// FONTSIZES

// Overview of all fontsizes
// submaps are used for responsive sizes
$fallbackFontSize: (
	root: 0.9rem,
	halfRoot: 0.45rem,
	small: 0.775rem,
	extrasmall: 0.65rem,
	menuLabelsExtraSmall: 0.41rem,
	menuLabelsSmall: 0.62rem,
	menuLabelsMedium: 0.63rem,
	menuLabels: 0.8rem,
	h1: (
		all: 1.5rem,
		small: 1.7rem,
		large: 2rem,
	),
	h2: (
		all: 1.1rem,
		small: 1.2rem,
		large: 1.3rem,
	),
	h3: (
		all: 0.95rem,
		small: 1rem,
		large: 1.05rem,
		extraExtraLarge: 1.1rem,
	),
	h4: (
		all: 0.9rem,
	),
	brandBarPageTitle: 1.75rem,
	introText: 1.05rem,
	payOff: 0.9rem,
);

// Example:
// font-size: getFontSize(h1 small);
@function getFallbackFontSize ($name) {
	@return mapDeepGet($fallbackFontSize, $name);
}

$lineHeightMap: (
	root: 1.6,
	heading: 1.2,
	formField: 1.2,
	button: 1,
	textSize: 1,

	// Specific element lineHeights
	labelListItem: 1.3,
	bigBlockMore: 1.7,
	searchTabItem: 1.5,
	transactionGemeenteAlternative: 1.4,
	anchorBlockLinkList: 0.5,
	subjectLinkListTitle: 1.7,
);

$fallbackLineHeightMap: (
	root: 1.75,
	heading: 1.3,
	formField: 1.3,
	button: 1.1,
	textSize: 1,

	// Specific element lineHeights	
	labelListItem: 1.4,
	bigBlockMore: 1.8,
	searchTabItem: 1.5,
	transactionGemeenteAlternative: 1.5,
	anchorBlockLinkList: 0.55,
	subjectLinkListTitle: 1.8,
);

@function getLineHeight($name) {
	@return mapDeepGet($lineHeightMap, $name);
}

@function getFallbackLineHeight($name) {
	@return mapDeepGet($fallbackLineHeightMap, $name);
}

@mixin lineHeight($lineHeight) {
	.is-webfont & {
		line-height: getLineHeight($lineHeight);

	}
	.is-fallbackfont & {
		line-height: getFallbackLineHeight($lineHeight);
	}
}
@mixin fontSize($size, $lineHeight: null) {
	.is-webfont & {
		font-size: getFontSize($size);

		@if ($lineHeight) {
			line-height: getLineHeight($lineHeight);
		}
	}
	.is-fallbackfont & {
		font-size: getFallbackFontSize($size);

		@if ($lineHeight) {
			line-height: getFallbackLineHeight($lineHeight);
		}
	}
}

@mixin fontSizeWithMq($mqType, $size) {
	.is-webfont & {
		@include setPropWithMq($mqType, font-size, getFontSize($size));
	}
	.is-fallbackfont & {
		@include setPropWithMq($mqType, font-size, getFallbackFontSize($size));
	}
}

@mixin fontSizeAlways($sizeValue) {
	.is-webfont &,
	.is-fallbackfont & {
		font-size: $sizeValue;
	}
}