.anchorBlock {
	$baseClass: #{&};
	@include insideMargin($quarterSpace);
	padding: 0 $halfSpace;
	border-top: 2px solid;
	position: relative;

	@include theme((border-top-color: foregroundDarker, background-color: backgroundTransparent), $baseClass, true);

	&-title {
		margin-top: 0;
		@include theme((color: foregroundDarker), $baseClass);
	}
	&-link {
		color: getColor(hemelblauw darkest);

		@include hoverEtc {
			color: getColor(hemelblauw darkest);
		}
	}

	@at-root {
		.interfaceLink-icon {
			@include theme((fill: foregroundDarker), $baseClass);
		}
	}
}