/* Headings */
/* stylelint-disable selector-no-qualifying-type */

$headingEmphasizedColor: getColor(hemelblauw default);

h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
	color: getColor(hemelblauw darkest);
	fill: getColor(hemelblauw darkest);
	font-weight: bold;

	@include lineHeight(heading);
}

h1, .h1 {
	color: getColor(hemelblauw default);
	fill: getColor(hemelblauw default);
	margin: {
		top: 0;
		bottom: 0.3em;
	}

	@include fontSizeWithMq(greater, h1);

	@include mqLesserThan(smallMedium) {
		hyphens: auto;
	}
}

h2, .h2 {
	margin: {
		top: 2em;
		bottom: 0.4em;
	}

	@include fontSizeWithMq(greater, h2);

	&.block-title {
		margin-top: 0;
	}
}

h3, .h3 {
	margin: {
		top: 2em;
		bottom: 0.4em;
	}
	@include fontSizeWithMq(greater, h3);

	&.block-title {
		margin-top: 0;
	}
}

h4, .h4,
h5, .h5,
h6, .h6 {
	@include fontSizeWithMq(greater, h4);
	margin: {
		top: 2em;
		bottom: 0.2em;
	}

	&.block-title {
		margin-top: 0;
	}
}

.heading {
	@include theme((color: foreground), #{&}, true);
}

.headingNoMargin {
	margin: 0;
}

.sideHeading {
	color: getColor(grijs darkest);
	fill: getColor(grijs darkest);
	margin-top: $pad-half;
}

.headingPrefix {
	font-weight: normal;
	@include fontSizeAlways(0.75em);
}

.headingEmphasized {
	color: $headingEmphasizedColor;
	fill: $headingEmphasizedColor;
}