$closeButtonForegroundColor: getColor(hemelblauw darkest) !default;
$closeButtonBackgroundColor: getColor(wit) !default;
$closeButtonOnDarkColor: getColor(wit) !default;
$closeButtonAlternativeBackgroundColor: getColor(hemelblauw lighter) !default;

.close {
	transition: transform 0.2s ease-in-out;
	padding: 0;
	background: none;
	border: none;
	width: $menuBarWidthSmall;
	height: $menuBarWidthSmall;
	padding: ($menuIconSizePaddingRatioSmall * $menuBarWidthSmall);
	background: $closeButtonBackgroundColor;
	color: $closeButtonForegroundColor;
	fill: $closeButtonForegroundColor;
	overflow: hidden;

	@include mqGreaterThan(small) {
		width: $menuBarWidthLarge;
		height: $menuBarWidthLarge;
		padding: ($menuIconSizePaddingRatioLarge * $menuBarWidthLarge);
	}

	@include hoverEtc {
		transform: scale(1.1);
		background: $closeButtonBackgroundColor;
		color: $closeButtonForegroundColor;
		fill: $closeButtonForegroundColor;
	}

	&--onDark {
		color: $closeButtonOnDarkColor;
		fill: $closeButtonOnDarkColor;

		@include hoverEtc {
			color: $closeButtonOnDarkColor;
			fill: $closeButtonOnDarkColor;
		}
	}

	&--withText {
		width: auto;
	}

	&--absoluteRight {
		position: absolute;
		top: 0;
		right: $halfSpace;
	}

	&-text {
		@include accHide;

		&-show {
			color: $closeButtonForegroundColor;
			font-weight: bold;
			margin-right: 0.2rem;
		}
	}
}

.close-icon {
	width: 100%;
	height: 100%;
	fill: $closeButtonForegroundColor;
	pointer-events: all;

	.btn & {
		fill: inherit;
	}

	&--withText {
		width: 1.5168rem;
		height: 1.5168rem;
	}

}

.simpleClose-icon {
	width: 1.5rem;
	height: 1.5rem;
	@include theme((fill: foreground), #{&}, true);

	@include hoverEtc {
		transform: scale(1.1);
		transition: transform 400ms;
	}
}


.feedback-close,
.enquete-close,
.share-close,
.cookieAlert-close {
	$iconResize: 3 / 4;
	position: absolute;
	right: $halfSpace;
	transform-origin: 50% 0;
	top: 0;
	padding: ($iconResize * $menuIconSizePaddingRatioSmall * $menuBarWidthSmall);
	width: $iconResize * $menuBarWidthSmall;
	height: $iconResize * $menuBarWidthSmall;
	background: $closeButtonAlternativeBackgroundColor;

	@include mqGreaterThan(small) {
		padding: ($iconResize * $menuIconSizePaddingRatioSmall * $menuBarWidthLarge);
		width: $iconResize * $menuBarWidthLarge;
		height: $iconResize * $menuBarWidthLarge;
	}

}