$breadcrumbLinkTextColor: getColor(wit) !default;
$breadcrumbLinkHoverTextColor: getColor(wit) !default;
$breadcrumbLinkSeparatorTextColor: getColor(wit) !default;

.breadcrumbs {
	@include noBullets;
	margin: 0 1rem 0 0;
	padding: 0.5rem 0;
}

.breadcrumb {
	padding: 0;
	display: inline;
	@include fontSize(small);

	@include mqLesserThan(large) {
		// nth-child magic :)
		// if it is the first child and there are 2 or more children after
		&:first-child:nth-last-child(n + 3) {
			.breadcrumb-link {
				&:after {
					content: '>';
					display: inline-block;
					padding-left: $pad-quarter;
					color: $breadcrumbLinkSeparatorTextColor;
				}
			}
			&:after {
				content: '';
				display: inline-block;
				padding-right: $pad-half;
				color: $breadcrumbLinkSeparatorTextColor;
			}
		}

		&:nth-child(n + 2) {
			display: none;
		}

		&:last-child {
			display: inline;
		}
	}
}

.breadcrumb-link {
	@include hideUnderline;
	display: inline;
	padding: 0 $pad-half 0 0;
	color: $breadcrumbLinkTextColor;
	word-break: break-word;

	@include hoverEtc {
		color: $breadcrumbLinkHoverTextColor;
	}

	&:before {
		content: '>';
		display: inline;
		padding-right: $pad-quarter;
		color: $breadcrumbLinkSeparatorTextColor;
	}
	&.breadcrumb-linkFirst {
		&:before {
			content: '';
			display: none;
		}
	}
}

.breadcrumb-text {
	@include hideUnderline;

	@include hoverEtc {
		@include underline;
	}
}