$feedbackSavedTextLinkColor: getColor(zwart) !default;
$feedbackSavedTextLinkIconColor: getColor(zwart) !default;

.feedback-textField {
	margin-top: 1rem;

	.formField-label {
		@include accHide;
	}

	.formField-wrapper {
		display: block;
		width: calcColumnWidth(1/1);
	}

	.formField-optional {
		display: block;
		text-align: right;
	}

	.formField-inputWrapper {
		display: block;
		width: 100%;
	}
}

.feedback-textField label {
	padding-bottom: 0;
}

.feedback-radio > p {
	margin-bottom: 0;
}

.feedback-saved {
	padding-right: 3.5rem;
}

.feedback-saved-title {
	margin-top: 0;
	color: inherit;
}

.feedback-saved-text {
	margin: 0;
}

.feedback {
	a:hover, a:focus, a:active {
		color: $feedbackSavedTextLinkColor;
		fill: $feedbackSavedTextLinkIconColor;
	}
}

.feedback-textWrapper:not(:empty) + .feedback-contactmessage {
	margin-top: 1rem;
}