.eventMeetingAside {

	&Block {
		@include mqLesserThan($threeColumnGridBreakpoint) {
			width: calc(100% - 2rem);
		}
		margin-top: 2rem;
	}

	@include theme((color: foreground, fill: foreground), #{&}, true);

	&-interfaceLinks {
		margin-top: 1rem;
		margin-bottom: 1rem;
		font-weight: bold;
	}

	&-availability {
		margin-top: 1rem;
		color: getColor(rood default);
		font-weight: bold;
	}

	&-registerButton {
		margin: 1rem 0 0 0;
		align-self: flex-start;
		@include theme((background-color: foreground), '.eventMeetingAside');
		font-weight: bold;

		@include hoverEtc {
			@include theme((background-color: foregroundLighter), '.eventMeetingAside');
			color: white;
		}
	}
}

.h4 {
	@include theme((color: foreground), '.eventMeetingAside');
}


.eventBlock {
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: stretch;

	&-title {
		min-height: 3.7rem;
		.h3 {
			@include hoverEtc {
				color: getColor(hemelblauw default);
				transition: color 400ms;
			}
		}
	}

	&-link {
		display: inline-block;
		border-bottom: 0;
	}

	&-meeting {
		min-height: 6rem;
	}

	&-desc {
		min-height: 5.5rem;
	}
}

.calendar-icon {
	@include mqLesserThan($twoColumnGridBreakpoint) {
		display: none;
	}
}

.meetingResults {
	overflow-y: auto;
	-webkit-overflow-scrolling: touch;

	@include mqGreaterThan($cardBreakpoint) {
		flex: 0 1 auto;
	}

}

.meetingResult {
	$baseClass: #{&};

	border-top: 3px solid;
	position: relative;

	@include theme((border-top-color: foreground), $baseClass, true);

	@include mqGreaterThan($cardBreakpoint) {
		border-top-width: 1px;
	}

	&-link {
		transition: background-color 400ms;
		display: block;
		border: none;
		color: inherit;

		@include hoverEtc {
			background-color: getColor(paars lightest);
			color: inherit;
		}
	}


	&-spacedLink {
		@include mqLesserThan($cardBreakpoint) {
			padding-bottom: 3.9rem;
		}

		@include mqGreaterThan($cardBreakpoint) {
			display: flex;
			align-items: flex-start;

			min-height: 4.4rem;
			padding-right: 11.2rem;
		}
	}

	&-dateTime {
		@include mqGreaterThan($cardBreakpoint) {
			width: 30%;
			min-width: 6rem;
			padding-right: $quarterSpace;
			flex: 0 0 auto;

			&-item {
				display: block;
			}
		}
	}

	&-title {
		display: block;
		@include fontSizeWithMq(greater, h3);
		@include theme((color: foreground), $baseClass);
		margin-top: 0.1rem;
		@include mqGreaterThan($cardBreakpoint) {
			padding-right: $quarterSpace;
			padding-left: $quarterSpace;
			flex: 1 0 auto;
		}
	}

	&-availability {
		@include mqGreaterThan($cardBreakpoint) {
			padding-right: $quarterSpace;
			padding-left: $quarterSpace;
			flex: 0 0 auto;
			width: 30%;
		}

	}

	&-signup {
		position: absolute;
		@include mqLesserThan($cardBreakpoint) {
			left: $halfSpace;
			bottom: $halfSpace;
		}
		@include mqGreaterThan($cardBreakpoint) {
			right: $halfSpace;
			top: $halfSpace;
		}
	}
}