.rijkslogo-link {
	margin-bottom: 0.8rem;
	border-bottom: 0;
	align-self: flex-start;

	svg {
		width: 30px;
		height: 53px;

		@include mqGreaterThan(small) {
			width: 36px;
			height: 63px;
		}

		@include mqGreaterThan(large) {
			width: 40px;
			height: 70px;
		}

		@include mqGreaterThan(extraExtraExtraLarge) {
			width: 44px;
			height: 77px; // fixed height needed for IE11
		}
	}
}

.rijkslogo-bg {
	fill: $rijksLogoBg;
}

.rijkslogo-weapon {
	fill: $rijksLogoWeapon;
}