.tabButton {
	@include cleanButton;
	padding: {
		left: 1rem;
		right: 1rem;
		top: 0.5rem;
		bottom: 0.5rem;
	}
	font-weight: bold;
	color: black;
	fill: getColor(hemelblauw default);

	@include hoverEtc {
		.tabButton-icon {
			position: relative;
			transform: translateY(2px);
		}
	}

	@include mqGreaterThan(medium) {
		margin-right: 1rem;
	}
}

.tabButton-icon {
	width: 1.5rem;
	height: 1.5rem;
	top: 0;
	transition: transform 200ms;
}

.tabButton--opened {
	background: getColor(wit);

	@include hoverEtc {
		.tabButton-icon {
			transform: translateY(-2px);
		}
	}
}

.tabButton-text {
	white-space: nowrap;
}