.situatie {

	.accordion {
		margin-top: 1rem;
	}

	&-storyOrImage {

		&.solo {
			height: 12rem;
			width: 17rem;
			overflow: hidden;
			margin-bottom: 1rem;
		}

		@include mqLesserThan(mediumLarge) {
			width: calc(100% - 2rem);
			margin-bottom: 1rem;
		}
	}

	.pageIntro {
		@include mqLesserThan(mediumLarge) {
			width: calc(100% - 2rem);
		}
	}
}

.verhaal {
	display: flex;
	border-top: 3px solid getColor(hemelblauw default);
	margin-left: 0;

	&-link {
		border-bottom: none;
	}

	&-title {
		margin: 0;
	}

	&-text {
		padding: {
			top: 0.5rem;
			left: 0.5rem;
		}
	}

	&-container {
		&-image {
			@supports(object-fit: cover) {
				object-fit: cover;
			}

			&-solo {
				width: 100%;
				@supports(object-fit: cover) {
					object-fit: cover;
				}
			}
		}
	}
}