.sector {
	&-overzicht {

		&-pageIntro {
			padding: {
				top: 1rem;
				bottom: 1rem;
			}
		}

		&-list {
			&-item {
				border-bottom: none;

				.promoBlock-figure:after {
					background: rgba(getColor(paars default),0.4);
				}

				@include hoverEtc {
					.promoBlock-figure:after {
						width: 100%;
						left: 0;
					}
				}
				&-image {
					height: 10rem;
					overflow: hidden;
					position: relative;

					.sector-image {
						width: 100%;
						@supports(object-fit: cover) {
							object-fit: cover;
							height: 100%;
						}
					}
				}
				&-link {
					flex: 1 1 auto;
					color: getColor(paars default);
					fill: getColor(paars default);
					font-weight: bold;
					display: flex;
					margin-top: 0.6rem;
					padding-right: 2rem;

					.goIcon {
						margin-right: 1rem;
					}

					&-moreInfo {
						position: absolute;
						right: 0;
						bottom: 0;
						@include cleanButton;
						background-color: getColor(wit);
						border-radius: 50%;
						fill: getColor(paars default);
						transition: transform 200ms;
						padding: 6px;
						@include hoverEtc {
							transform: scale(1.1);
							transition: transform 200ms;
						}
						svg {
							width: 1rem;
							height: 1rem;
						}

						@include mqLesserThan(medium) {
							bottom: -4px;
							padding: 0.6rem;
						}
					}

				}
			}

			&-sector {

				margin-bottom: 1.5rem;
				position: relative;

				.interfaceLink {
					@include hoverEtc {
						color: getColor(hemelblauw default);
					}
				}

				.featured & {
					border-top: 5px solid getColor(paars default);
				}

				.promoBlock-goLink {
					margin: {
						top: 0.5rem;
						bottom: 0.5rem;
					}
				}

			}

			&-popUp {
				background-color: white;
				border: 1px solid getColor(paars default);
			}
		}

		&-bg {
			background-color: getColor(paars lightest);
			margin-bottom: 1rem;
		}

		&-nonFeatured {
			.sector-overzicht-list-item-link-moreInfo {
				fill: getColor(hemelblauw default);
				border: 1px solid getColor(hemelblauw default);
				padding: 0.25rem;

				@include hoverEtc {
					fill: getColor(wit);
				}

				@include mqLesserThan(medium) {
					bottom: 0;
					padding: 0.5rem;
				}
			}

			.interfaceLink {
				color: getColor(hemelblauw darkest);
			}
		}

	}
}

.sectorDetailPage {
	@include insideMarginBottom($space);

	h2 {
		color: getColor(paars default);
	}
}