.downloadLink {
	border-bottom: none;
	margin-top: $space;
	margin-bottom: $space;
	transition: none;
}

.downloadLink-dlIcon {
	background: getColor(hemelblauw default);
	border-radius: 99999px;
	height: 2.4rem;
	width: 2.4rem;
	padding: 0.3rem 0.4rem 0.5rem 0.4rem;

	svg.dlIcon { /* stylelint-disable-line selector-no-qualifying-type */
		width: 100%;
		height: 100%;
		fill: getColor(wit);
	}
}

.downloadLink-description {
	padding-left: $halfSpace;
}

.downloadLink-info {
	display: inline-block;
	margin-left: 0.8rem;
	font-weight: normal;
	font-size: getFontSize(small);
}

.downloadLink-fileTypeIcon {
	display: inline-block;
	vertical-align: middle;
	width: 1.1em;
	height: 1.1em;
}

.downloadLink-fileType {
	margin-right: 0.1rem;
}

.downloadLink-text {
	margin: 0.8rem 0 0 0;
}