.biographyBlock {
	padding-top: 0;

	&-image {
		width: 5rem;
		height: 5rem;
		margin-right: $halfSpace;
		max-width: none;
	}

	&-text {
		margin: $halfSpace $halfSpace $halfSpace 0;
	}

	&-social {
		display: block;
		margin: $quarterSpace 0;

		&-figure {
			width: 2rem;
			height: 2rem;
			position: relative;
			display: inline-block;
		}
	}
}