.skipLinks {
	margin: 0;
	position: relative;
}

.skipLink {
	@include accHide;
	@include zIndex(overlay);

	padding: 0.2rem 0.75rem 0.3rem 0.75rem;

	background-color: getColor(violet default);
	color: getColor(wit);
	transform: translateX(-50%);
	display: block;
	width: max-content;

	&:focus {
		left: 50%;
		top: $halfSpace;
	}

	@include hoverEtc {
		color: getColor(wit);
	}
}