.show {
	&-mobile {
		display: none;
		@include mqLesserThan($twoColumnGridBreakpoint) {
			display: block;
		}
	}

	&-medium {
		display: block;
		@include mqGreaterThan(medium) {
			display: none;
		}
	}

	&-desktop {
		display: block;
		@include mqLesserThan($twoColumnGridBreakpoint) {
			display: none;
		}
	}
}