.coenInfoGraphicContainer {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 0;

	@include mqGreaterThan($twoColumnGridBreakpoint) {
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: space-around;
		align-items: flex-start;
	}
}

.coenInfoGraphic {
	width: 90%;
	margin: $halfSpace 0;

	@include mqGreaterThan($twoColumnGridBreakpoint) {
		width: 40%;
		margin-left: 5%;
		margin-right: 5%;

		&:first-child {
			width: 50%;
		}
	}
}

.oldCoenReportDescription {
	margin-left: $halfSpace;
}