.showCaseMedia-header {
	padding-top: 5rem;
	padding-bottom: 1rem;
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	background-position: center center;
	background-size: cover;
	color: $storyHeaderTextColor;

	.playButton {
		visibility: hidden;
	}

	.overviewPageTitle {
		width: 100%;
		position: absolute;
		z-index: 5;
		top: 0;
		left: 0;
	}

	@include before {
		content: '';
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-image: linear-gradient(0deg,rgba(3,48,84,0.95),rgba(3,48,84,0.10));
		z-index: 0;
	}

	@include mqGreaterThan(mediumLarge) {
		height: 75vh;
		padding-top: 0;
		padding-bottom: 5vh;

		.playButton {
			visibility: visible;
		}
	}
}

.showCaseMedia-header--blue {
	@include before {
		background-image: linear-gradient(0deg,rgba(3,48,84,0.95),rgba(3,48,84,0.10));
	}
}

.showCaseMedia-header--purple {
	@include before {
		background-image: linear-gradient(0deg,rgba(#42145f,0.95),rgba(#42145f,0.10));
	}
}

.showCaseMedia-header--grey {
	@include before {
		background-image: linear-gradient(0deg,rgba(#8f8f8f,0.95),rgba(#8f8f8f,0.10));
	}
}

.showCaseMedia-header--green {
	@include before {
		background-image: linear-gradient(0deg,rgba(#727800,0.95),rgba(#727800,0.10));
	}
}

.showCaseMedia-header--violet {
	@include before {
		background-image: linear-gradient(0deg,rgba(#a90061,0.95),rgba(#a90061,0.10));
	}
}

.showCaseMedia-link-wrapper {
	border: 0;
	
	@include hoverEtc {
		color: $storyHeaderTextColor;
	}
}

.showCaseMedia-content-wrapper {
	position: relative;
	z-index: 5;
}

.showCaseMedia-label {
	@include cleanButton;
	width: 6rem;
	margin-bottom: 0.5rem;
	padding: 0.5rem 1rem;
	background-color: getColor(wit);
	@include theme((color: foreground), '.showCaseMedia-header');
	font-weight: bold;
	text-align: center;

	@include hoverEtc {
		background-color: getColor(wit);
	}
}

.showCaseMedia-person-info {
	margin-bottom: 0.5rem;

	.social-link,
	.social-figure {
		width: 1.5rem;
		height: 1.5rem;
	}

	.social-link {
		margin: 0 0.5rem 0 0;
	}

	.social-figure-foreground {
		width: 0.8rem;
		height: 0.8rem;
		top: 0.35rem;
		left: 0.35rem;
	}
}

.showCaseMedia-person-name,
.showCaseMedia-person-age {
	font-weight: bold;

	@include fontSizeWithMq(greater, h3);
	@include lineHeight(textSize);
}

.showCaseMedia-title {
	position: relative;
	color: $storyHeaderTextColor;

	&.hasQuotes {
		@include before {
			content: '\201C';
			position: relative;
			margin-right: 0.1em;
			@include lineHeight(textSize);
		}

		@include after {
			content: '\201D';
			position: relative;
			margin-left: 0.1em;
			@include lineHeight(textSize);
		}

		@include mqGreaterThan(mediumLarge) {
			padding-left: 1em;

			@include before {
				margin-right: 0;
				position: absolute;
				left: 0;
				@include fontSizeAlways(2em);
			}
			@include after {
				height: 0.5em;
				margin-left: 0;
				padding-left: 0.1em;
				position: absolute;
				bottom: 0.2em;
				right: auto;
				@include fontSizeAlways(2em); //relative to title font size
			}
		}
	}
}

.showCaseMedia-intro {
	@include mqGreaterThan(extraLarge) {
		width: 75%;
	}
}

.showCaseMedia-link {
	font-weight: bold;
}

.showCaseMedia-meta-info {
	margin-left: 1rem;
	font-weight: normal;
}

.showCaseMedia-body {
	padding-top: 3rem;

	h2:first-child,
	h3:first-child {
		margin-top: 0;
	}
}

.showCaseMedia-footer {
	margin-top: 1rem;
	margin-bottom: 2rem;
	border-top: 1px solid $storyFooterBorderColor;

	h2:first-child,
	h3:first-child {
		margin-top: 1.5rem;
	}
}

.showCaseMedia-related-quote {
	margin-top: 0.75rem;

	@include before {
		content: '\201C'; // Open quote
	}

	@include after {
		content: '\201D'; // Sluit quote
	}
}

.showCaseMedia-related-link {
	border-bottom: 0;
}

.playButton {
	@include centerAbsolute(5rem, 5rem);
	transition: all 0.25s ease-in-out;
	
	.promoBlock & {
		@include centerAbsolute(3rem, 3rem);
	}

	@include theme((fill: foreground), '.promoBlock');
	@include theme((fill: foreground), '.showCaseMedia-header');

	.playButtonBg {
		opacity: 0.75;
		transition: all 0.25s ease-in-out;

		.showCaseMedia-header:hover & {
			opacity: 0.9;
		}
	}

	.showCaseMedia-header:hover &, {
		transform: scale(1.05);
	}
}