.deadLink {
	display: flex;
	justify-content: center;
	align-items: center;
	position: fixed;
	width: 100%;
	height: 100%;
	background-color: rgba(getColor(hemelblauw light),0.9);
	top: 0;
	left: 0;
	padding: 5rem;
	@include zIndex(dialog);


	strong {
		display: flex;
		position: relative;
		@include fontSizeAlways(200%);
		@include fontSizeAlways(200%);
	}

	button {
		background-color: getColor(wit);
		margin-left: 5%;
		padding: 0.4rem 1rem;
		color: getColor(hemelblauw darkest);
		transition: transform 0.5s ease-in-out;
		border: none;
	}

	button:hover {
		transform: scale(1.05);
		color: getColor(zwart);
	}

}

