.date-display {
	&__container {
		margin-right: 1rem;
		position: relative;
	}

	&__icon {
		width: 4rem;
		height: 4rem;
	}

	&__date {
		width: 2.4rem;
		height: 2rem;
		background-color: getColor(wit);
		position: absolute;
		top: 1.7rem;
		left: 0.8rem;
		@include lineHeight(textSize);
		font-weight: bold;
		font-family: 'Arial Black', 'Arial Bold', Arial, sans-serif;
		@include fontSize(halfRoot);
		text-align: center;
		@include theme((color: foreground), #{&}, true);

		&__day {
			display: block;
			@include fontSize(root);
		}
		&__month {
			display: block;
			@include fontSize(extrasmall);
		}
	}
}