.articleOverview {
	width: 100%;
}

.articleOverview--grey {
	.articleOverview-acc-warning {
		color: getColor(grijs grijs6);
		font-style: italic;
	}
}

.articleOverview-acc-title {
	margin: 0;
	color: getColor(hemelblauw default);
	line-height: inherit;
	@include fontSizeAlways(inherit);
	font-weight: bold;
}

.articleOverview-acc-warning {
	color: getColor(violet default);
	font-style: italic;
}

.articleOverview-acc-list {
	margin-left: 0;
	@include noBullets;
}

.articleOverview-acc-item {
	margin-bottom: 1rem;

	@include hoverEtc {
		.articleOverview-acc-title {
			text-decoration: underline;
		}
	}
}