$gutter: 3rem !default;
$mobileGutter: 1rem !default;
$baseColor: getColor(hemelblauw default) !default;
$buttonColor: getColor(violet default) !default;
$filterIconWidth: 2rem !default;
$searchResultsMoreButtonIconFill: getColor(wit) !default;

.search-header {
	flex: 0 0 auto;
	margin-top: 3.3rem;
	@include mqGreaterThan($twoColumnGridBreakpoint) {
		margin-top: 4.8rem;
	}
}

.search-header-title {
	@extend .h2;
	margin-top: 0;
}

.search-gutter {
	margin: {
		left: $gutter;
		right: $gutter;
	}

	@include mqLesserThan($twoColumnGridBreakpoint) {
		margin: {
			left: $mobileGutter;
			right: $mobileGutter;
		}
	}
}

.search-interface {
	&#{&} {
		position: relative;
		margin-bottom: 2rem;
		align-items: stretch;
		max-width: 40rem;
	}
}

.search-input {
	padding-left: 1rem;
	border: 1px solid getColor(hemelblauw darkest);

	&::-webkit-search-cancel-button,
	&::-webkit-search-decoration {
		-webkit-appearance: searchfield-cancel-button;
	}
	&::-ms-clear {
		display: block;
	}
}

.search-clear-input {
	position: absolute;
	width: 1.5rem;
	height: 1.5rem;
	top: 11px;
	right: 18%;
	@include mqLesserThan($twoColumnGridBreakpoint) {
		display: none;
	}
}

.search-button {
	@include moreButton($buttonColor);
	width: auto;
}

.search-button-icon {
	display: none;
}

.search-suggestions {
	background-color: getColor(wit);
	border: 1px solid getColor(hemelblauw darkest);
	border-top: none;
	padding-left: 1rem;
}

.search-suggestions-list {
	@include noBullets;
}

.search-suggestion-link {
	text-decoration: none;
	border: none;
	display: flex;
}

.search-suggestion-icon {
	transform: rotate(45deg);
}

.search-tabs {

	@include mqLesserThan($twoColumnGridBreakpoint) {
		display: none;
	}

	&.tabs {
		margin-left: $gutter;
	}
}

.search-tabs-item {
	@include cleanButton;
	width: 100%;
	@include lineHeight(searchTabItem);
}

.search-tab {
	background-color: $baseColor;
	color: getColor(wit);
	transition: all 0.3s ease-in-out;
	display: flex;

	&:hover {
		background-color: darken($baseColor, 7%);
	}

	.tab-link {
		padding: 0.3rem 1.4rem;
		//overwrite a-rules
		transition: none;
		//overwrite tab-link styles from ow-pagina

		&:before {
			position: relative;
			background: none;
		}
		&:hover {
			color: getColor(wit);
			fill: getColor(wit);
		}

		.tab-icon {
			margin-left: -0.8rem;
			margin-right: 1rem;
			font-size: 1.4rem;
		}
	}
}

.search-tab-text,
.search-tab-subtext {
	display: block;
}

.search-tab-subtext {
	font-size: 0.75rem;
	font-weight: normal;
}

.search-tab-total {
	margin-left: 1.4rem;
}

.search-tab-active {

	&:hover {
		background-color: getColor(wit);
	}

	.tab-link {
		border-top: 3px solid darken($baseColor, 7%);
		font-weight: bold;
		color: getColor(hemelblauw darkest);
		fill: getColor(hemelblauw darkest);
		background-color: getColor(wit);
		border-bottom: none;
		padding-top: 0.48rem;

		@include hoverEtc {
			color: getColor(hemelblauw darkest);
			fill: getColor(hemelblauw darkest);
		}

		&:before {
			position: relative;
			background: none;
		}
	}
}

.search-filters {

	@include mqLesserThan($twoColumnGridBreakpoint) {
		background: white;
		border-bottom: 1px solid getColor(grijs border);
	}
}

.search-filters-mobile {
	display: none;
	position: relative;
	margin-left: 1rem;
	margin-right: 1rem;

	@include mqLesserThan($twoColumnGridBreakpoint) {
		display: flex;
		.filter-icon {
			width: $filterIconWidth;
			height: 1.5rem;
			position: absolute;
			top: 0;
			left: 0;
			bottom: 0;
			margin-top: auto;
			margin-bottom: auto;
		}

		.select-icon {
			width: 2rem;
			height: 2rem;
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			margin-top: auto;
			margin-bottom: auto;
			fill: getColor(hemelblauw darkest);
		}
	}
}

.search-filters-mobile-select {
	width: 100%;
	font-weight: bold;
	color: getColor(hemelblauw default);
	padding: {
		top: 0.8rem;
		bottom: 0.8rem;
		left: $filterIconWidth;
	}
	appearance: none;
	background: none;
	border: none;
	position: relative;
}

.search-filters-mobile-option {
	color: getColor(hemelblauw darkest);
	&:checked {
		font-weight: bold;
	}
}

.search-results {
	background-color: getColor(wit);
	padding-bottom: $gutter;
	position: relative;
	top: 0;
	flex: 1 1 0;
	overflow-y: auto;
	-webkit-overflow-scrolling: touch;
}

.search-results-list {
	transition-property: opacity;
	transition-duration: 200ms;
	list-style: none;
	padding-top: $space;
	padding-left: 0;

	li:before {
		content: '';
	}
}

.search-results-message {
	padding-top: $space;
}

.search-results-item {
	display: flex;
}

.search-results-item-link {
	display: block;
	margin-left: 1rem;
	border-bottom: 0;
	@include mqLesserThan(medium) {
		margin-left: 0;
	}
}

.search-results-item-icon {
	flex: 0 0 auto;
	svg {
		height: 2rem;
		width: 2rem;
		fill: getColor(hemelblauw darkest);
	}
	@include mqLesserThan(medium) {
		display: none;
	}
}

.search-results-item-title {
	margin-top: 0.5rem;
}

.search-results-item-target {
	font-style: italic;
	display: block;
	color: $baseColor;
	word-break: break-all;
	word-break: break-word;
}

.search-results-more-button {
	margin-left: 5.6rem;
	margin-bottom: 2rem;
	background-color: $buttonColor;
	border: none;
	border-top: 3px solid darken($buttonColor, 7%);
	&:hover {
		background-color: darken($buttonColor, 7%);
	}
	@include mqLesserThan(medium) {
		margin-left: 1rem;
	}
}

.search-results-more-button-icon {
	@include rotateYoYo;
	fill: $searchResultsMoreButtonIconFill;
	margin-left: 0.5em;
}

.search-results-amount {
	margin: {
		top: 1rem;
	}
	min-height: 3rem;

	@include mqLesserThan(medium) {
		display: none;
	}
}

.search-results-amount-number {
	color: getColor(hemelblauw darkest);
}

.search-results-query {
	font-weight: bold;

	&:before {
		content: '';
		font-weight: normal;
	}

	&:after {
		content: '';
		font-weight: normal;
	}
}

.search-facetSelectorIcon {
	width: 1rem;
	height: 1rem;
	display: none;

	.search-tab-active & {
		@include mqLesserThan(medium) {
			display: block;
		}
	}
}

.search-error {
	margin-bottom: 1rem;
}

.search-errorIcon {
	width: 1.5rem;
	height: 1.5rem;
	fill: getColor(rood default);
}

.search-errorText {
	margin-left: 0.2em;
	margin-top: 0.1em;
	// Enough contrast when text is bold
	color: getColor(rood default);
	font-weight: bold;
}