$homeHeroBackground: getColor(hemelblauw lighter) !default;
$homeHeroGradientStop1: rgba(getColor(zwart), 0.4) !default;
$homeHeroGradientStop2: rgba(getColor(zwart), 0) !default;
$homeHeadingTextColor: getColor(wit) !default;
$homeHeadingShadowColor: getColor(zwart) !default;


.homepageHeader {
	&-container {
		padding-top: 2rem;
		padding-bottom: 1.5rem;
	}

	&--has-image {
		.homepageHeader-container {
			min-height: 20rem;
			padding-top: 0;
			padding-bottom: 0;
		}

		.topTasks {
			width: 100%;

			@include mqGreaterThan(extraExtraLarge) {
				width: calcColumnWidth(2/3);
			}
		}
	}

	&-topTasks {
		position: relative;
		@include insideMargin(1.5rem 1.5rem);
		@include mqGreaterThan(smallMedium) {
			@include insideMarginBottom(2.5rem);
			padding-bottom: 3rem;
		}

		@include setPropWithMq(greater, width,
			(
				all: calcColumnWidth(1/1),
				smallMedium: calcColumnWidth(2/3)
			)
		);
	}

	&-background {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		overflow: hidden;

		&-image {
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			background-size: cover;
			background-repeat: no-repeat;
			background-position: 66% 90%;

			@include mqLesserThan(mediumLarge) {
				left: $menuBarWidthLarge;
			}

			@include mqLesserThan(smallMedium) {

				@include after('') {
					position: absolute;
					left: 0;
					right: 0;
					top: 0;
					bottom: 0;
					background-image: linear-gradient(
						to bottom,
						hsla(0, 100%, 100%, 0.5),
						hsla(0, 100%, 100%, 0) 44%
					);
				}
			}

			@include mqLesserThan(small) {
				left: $menuBarWidthSmall;
			}

		}
	}
}

.homeHeading {
	margin-top: 1.5rem;
	margin-bottom: 2rem;
	color: $homeHeadingTextColor;
	align-self: flex-start;
	text-shadow: 0 0 10px $homeHeadingShadowColor;
}

.homeTopTasksWrapper {
	align-self: flex-end;
}

.homeHeroContainer,
.homeHeroForeground {
	position: relative;
	min-height: 40vw;
	@include mqGreaterThan(mediumLarge) {
		min-height: 30vw;
	}
	@include mqGreaterThan(extraLarge) {
		min-height: 25vw;
	}
}

.homeHeroContainer {
	background-color: $homeHeroBackground;
}

.homeHeroImage:after {
	content: '';
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;

	background-image: linear-gradient(to top, $homeHeroGradientStop1, $homeHeroGradientStop2 50%);
}