// Visibility utilities
.u-visible-mobile {
	display: none !important; /* stylelint-disable-line declaration-no-important */

	@include mqLesserThan(smallMedium) {
		display: block !important; /* stylelint-disable-line declaration-no-important */
	}
}

.u-hidden-mobile {
	display: block !important; /* stylelint-disable-line declaration-no-important */

	@include mqLesserThan(smallMedium) {
		display: none !important; /* stylelint-disable-line declaration-no-important */
	}
}

// Display utilities
.u-displayBlock {
	display: block;
}

.u-noFlex {
	flex: none;
}

.u-unflex {
	flex: 0 0 auto;
}

// Margin and padding utilities
.u-noMargin {
	margin: 0;
}

.u-noMarginTop {
	margin-top: 0;
}

.u-marginTop-1 {
	margin-top: 1rem;
}

.u-marginTop-2 {
	margin-top: 2rem;
}

.u-marginBottom-1 {
	margin-bottom: 1rem;
}

.u-marginBottom-2 {
	margin-bottom: 2rem;
}

.u-noPadding {
	padding: 0;
}

// Styling utilities
.u-noBullets {
	@include noBullets;
}