.tooltip-bulb {
	@include cleanButton;
	padding: 6px;
	border-radius: 50%;
	border: 1px solid getColor(hemelblauw default);
	transition: transform 200ms;
	fill: getColor(hemelblauw default);

	@include hoverEtc {
		transform: scale(1.1);
	}

	svg {
		width: 1rem;
		height: 1rem;
	}

	.featured & {
		fill: getColor(paars default);
		background-color: getColor(wit);
		border-color: getColor(wit);
	}
}