$icon-size: 0.7rem;

@mixin mqDisableSeeThroughScroll {
	@include mqGreaterThan(smallMedium) {
		@include mqGreaterThan(mediumHeight) {
			@content;
		}
	}
}

.search-close {
	@include zIndex(menuBar content buttons);
	left: $halfSpace;
	position: absolute;

	.close-icon {
		fill: getColor(hemelblauw darkest);
	}
}

.mainMenu {
	$menuItemPadding: 0.2rem;
	display: flex;
	flex-direction: column;
	height: 100%;
	position: relative;

	@include mqGreaterThan(smallMedium) {
		overflow-y: auto;
		-webkit-overflow-scrolling: touch;
	}


	&-close {
		@include zIndex(menuBar content buttons);
		left: $halfSpace;
		position: absolute;

		.close-icon {
			fill: getColor(hemelblauw darkest);
		}
	}

	&-nav {
		height: 100%;

		.mainMenu-desktop {
			@include fontSize(small);
		}

		@include mqDisableSeeThroughScroll {
			margin-top: $menuBarWidthSmall + 1.75rem;
			padding-left: 4rem;
		}

		@include mqGreaterThan(smallMedium) {
			display: flex;
			flex-direction: column;
			flex: 1 1 0;

			@include mqLesserThan(mediumHeight) {
				margin-top: $menuBarWidthSmall + 1.75rem;
				padding-left: 4rem;
			}
		}
	}

	.list--triple {
		@include setPropWithMq(greater, column-count,
			(
				smallMedium: 2,
				extraExtraExtraLarge: 3
			)
		);
	}

	&-title {
		flex: 0 0 auto;
		margin: 0;
		padding: $menuItemPadding $halfSpace;
		@include lineHeight(root);
		cursor: pointer;
		background: rgba(getColor(hemelblauw lighter), 0.9);

		&-container {
			display: flex;
			align-items: center;
		}

		&-icon {
			width: $icon-size;
			height: $icon-size;
			margin-right: 1rem;
			fill: getColor(hemelblauw darkest);
		}

		&-text {
			font-weight: bold;
			margin: 0;
		}

		.no-touch  & {
			@include hoverEtc {
				.mainMenu-title-text {
					@include underline;
				}
			}
		}

		@include mqDisableSeeThroughScroll {
			cursor: auto;
			margin-top: 1rem;
			padding: {
				top: ($menuItemPadding / 2);
				bottom: ($menuItemPadding / 2);
			}
			.no-touch  & {
				@include hoverEtc {
					.mainMenu-title-text {
						@include noUnderline;
					}
				}
			}
		}
	}

	&-button {
		@include cleanButton;
		display: inline-block;
		width: 2rem;
		height: 1.5rem;
		vertical-align: -31%;
		@include mqDisableSeeThroughScroll {
			display: none;
		}
	}

	&-content {
		flex: 0 1 auto;
		overflow: auto;
		overflow-x: hidden;
		padding: 0;

		&.ng-animate {
			overflow: hidden;
		}

		&-background {
			background: rgba(getColor(wit), 0.6);
			@include mqDisableSeeThroughScroll {
				background: none;
			}
		}
	}

	&-linkList {
		@extend %noBullets;
		column-gap: 0;

		padding: $menuItemPadding 0;
		margin: {
			left: 0;
			right: 0;
		}
		@include mqDisableSeeThroughScroll {
			padding: 0;
		}
	}

	&-item {
		margin: 0;
		padding: 2px; // to accommodate for the 2px outline when links get focus

		&:last-child, &:first-child {
			z-index: getZIndex(menuBar content scrollIndicatorHider);
			background: getColor(hemelblauw lighter);
			position: relative;
		}
	}

	&-link {
		display: block;
		@include mqGreaterThan(smallMedium) {
			display: inline-block;
		}
		@include mqLesserThan(smallMedium) {
			margin-left: 1rem + $icon-size;
		}
		@include noUnderline;
		padding: $menuItemPadding 0.875rem $menuItemPadding 0.625rem;
		color: getColor(hemelblauw grijscontrast);
		transition: color 0.2s;

		&.highlighted {
			background-color: white;
			font-weight: bold;
		}

		&-icon {
			width: 1.5rem;
			height: 1.5rem;
			margin: -0.2rem 0.8rem -0.2rem 0;
			position: relative;
			left: 0;
			fill: getColor(hemelblauw grijscontrast);

			transition: left 0.2s ease-in-out;

			&-chevron {
				width: 0.9rem;
				height: 0.9rem;
			}
		}

		@include hoverEtc {
			color: getColor(hemelblauw darkest);

			.mainMenu-link-icon {
				left: 5px;
				fill: getColor(hemelblauw darkest);
			}
		}
	}

	&-acc {
		&-content {
			&-title {
				@include cleanButton;
				width: 100%;
				padding: {
					top: 0.5rem;
					bottom: 0.5rem;
					left: 0.7rem;
					right: 0.5rem;
				}
				flex: 0 0 auto;
			}

			&-body {
				position: relative;
				@include noBullets;
				padding-bottom: 2rem;
				flex: 1 1 auto;
				overflow-y: auto;
				-webkit-overflow-scrolling: touch;
			}

			&-container {
				flex: 0 1 auto;
				display: flex;
				flex-direction: column;
				position: relative;

				&:before,
				&:after {
					content: '';
					position: absolute;
					left: 0;
					right: 0;
					width: 100%;
					pointer-events: none;
					z-index: getZIndex(menuBar content scrollIndicator);
				}

				&:before {
					top: 0;
					height: 0.75rem;
					background: linear-gradient(
						rgba(139, 189, 214, 1) 0%,
						rgba(getColor(hemelblauw lighter),1) 1%,
						rgba(getColor(hemelblauw lighter),0.9) 50%,
						rgba(255,255,255,0) 100%
					);
				}

				&:after {
					bottom: 0;
					height: 3.5rem;
					background: linear-gradient(
						rgba(255,255,255,0) 0%,
						rgba(getColor(hemelblauw lighter),0.9) 75%,
						rgba(getColor(hemelblauw lighter),1) 99%,
						rgba(139, 189, 214, 1) 100%
					);
				}
			}
		}
	}

	&-accordion {

		flex: 1 1 0;
		display: flex;
		flex-direction: column;

		.mainMenu-acc {
			border-top: none;

			&.isOpened {
				background-color: getColor(hemelblauw light);
			}

			+ .mainMenu-acc-content-body {
				background-color: white;
			}
		}
	}

	&-desktop {
		@include mqLesserThan(smallMedium) {
			display: none;
		}

		&-list {
			/* 
				Hack for Safari on Mac, to circumvent bug that 
				hides menu lists due to overflow-y on .MainMenu
			*/
			transform: translateZ(0);

			&-container:first-child {
				.mainMenu-title {
					margin-top: 0;
				}
			}
		}
	}

	&-mobile {
		display: none;
		@include mqLesserThan(smallMedium) {
			display: flex;
			overflow: auto; // to keep content visible when zooming in on smaller screens
			height: 100%;
			flex-direction: column;
			padding-top: 4rem;
		}

		.mainMenu-item {
			list-style-type: none;
		}

		.homeLink .mainMenu-link {
			margin-left: 1rem + $icon-size;
			color: getColor(hemelblauw darkest);
		}
	}
}