// COLORS

// Declaration of all colors
// grouped by major color group
$colors: (
	hemelblauw: (
		default: #0077c0,	// default -> hemelblauw, old value was #007bc7
		darkest: #033054,	// bestaat niet in Rijkshuisstijl
		darker: #2d526c,	// introduced for English 
		bright: #1575b5,
		official: #154273,	// official blue color Rijkslogo
		soft: #4983c6,
		light: #8fcae7,		// default -> licht blauw
		light2: #d9edf6,
		lighter: #e2f0f7, 	// light -> licht blauw
		lighter2: #ebf6fb,
		lightest: #f3f9ff,  // lichtst -> gebruikt in detailPagesBlock
		grijscontrast: #0670b2,
	),
	aqua: (
		default: #00857c,
	),
	violet: (
		default: #a90061,
		light: #e5b2cf,
		lightest: #f4e9f1
	),
	mosgroen: (
		darker: #686b00,
		default: #727800, // old value was #777c00
		light: #d6d7b2,
		lightest: #eeeed7,
	),
	paars: (
		darker: #310d47,
		default: #42145f,
		light: #5f3977,
		lightest: #d9c7e3,
	),
	mintgroen: (
		default: #76d2b6,
		light: #d6f1e9,
		lightest: #ebf8f4
	),
	grijs: (				// alle grijstinten wijken af van Rijkshuisstijl
		darkest: #333333,
		grijs6: #696969,	// uit Rijkhuisstijl (5.5 contrast op wit)
		grijs7: #777773,	// 4.5 contrast op wit
		default: #8f8f8f,
		rightcolumn: #444444,
		border: #e5e5e5,
		light: #dddddd,
		lightest: #f6f6f6
	),
	// (Formulier) meldingen: valid
	groen: (
		default: #39870c,
		light: #c3dbb6,
		lightest: #efeddb
	),
	// (Formulier) meldingen: invalid
	rood: (
		default: #d52b1e,
		light: #f2bfbb,
		lightest: #f9dfdd
	),
	zwart: #000000,
	wit: #ffffff,
	oranje: (
		default: #e47237,
		text: #ba5c2c, // heeft genoeg contrast voor kleine tekst op wit
		alternative: #e27000,
		light: #e88756,
		lightest: #ffeadf,
	),
	socials: (
		facebook: #3b5998,
		twitter: #5ea9dd,
		linkedin: #0274b3,
		googleplus: #da4835,
		youtube: #cc181e
	)
);

// Example:
// color: getColor(hemelblauw darkest);
@function getColor($color) {
	@return mapDeepGet($colors, $color);
}