$filterIconFillColor: getColor(wit) !default;
$filterDownIconBackgroundColor: getColor(violet default) !default;
$filterTabsBackgroundColor: getColor(wit) !default;
$filterTabsCloseBackgroundColor: getColor(hemelblauw lighter) !default;
$filterTabsCloseForegroundColor: getColor(hemelblauw darkest) !default;
$openFilterBackgroundColor: getColor(paars lightest) !default;
$openFilterForegroundColor: getColor(paars default) !default;
$filterCloseCardButtonBackgroundColor: $openFilterForegroundColor !default;
$filterCloseCardBackgroundColor: getColor(wit) !default;
$filterMobileCardBackgroundColor: getColor(wit) !default;

.filters {
	padding-bottom: 0.5rem;
}

.filter-accHide {
	@include accHide;
}

.filter-downIcon {
	position: absolute;
	display: flex;
	align-items: center;
	right: 0;
	top: 0;
	bottom: 0;
	background-color: $filterDownIconBackgroundColor;

	svg {
		width: 2.5rem;
		height: 2rem;
		fill: $filterIconFillColor;
	}
}

.filter-selected-close {
	display: flex;
	align-items: center;
	overflow: hidden;
}

.filter-selected-close-icon {
	width: 2.5rem;
	height: 1.5rem;
	display: block;
	fill: $filterIconFillColor;
}


// Onderstaande is voor filter-component


.filter-tabs {
	padding-top: 1rem;
	margin-bottom: 0;
	background: $filterTabsBackgroundColor;
}

.filter-tabs-options {
	padding-right: (3rem + $quarterSpace);
}

.filter-tabs-bottom {
	height: 4px;
	display: flex;
	justify-content: flex-end;
	align-items: flex-end;
}

.filter-tabs-close {
	background-color: $filterTabsCloseBackgroundColor;
	width: 3rem;
	height: 3rem;

	transform-origin: right bottom;

	@include hoverEtc {
		background-color: $filterTabsCloseBackgroundColor;
	}

	.close-icon {
		fill: $filterTabsCloseForegroundColor;
	}
}

.filter-remove {
	margin: auto 1rem auto auto;
}

.filterBar {
	align-items: flex-end;
}

.filter-events {
	margin-bottom: 1rem;
}

.filter-openFilter-container {
	color: $openFilterForegroundColor;
	fill: $openFilterForegroundColor;
}

.filter-openFilterButton {
	width: 100%;
	margin-bottom: 1rem;
	background-color: $openFilterBackgroundColor;

	@include hoverEtc {
		background-color: $openFilterBackgroundColor;
	}
}

.filter-openFilter-text {
	font-weight: bold;
	text-align: left;
	padding-left: 1rem;
}

.filter-icon-filter {
	width: 1.5rem;
	height: 1.5rem;
	position: relative;
	left: 0.2rem;
}

.filter-icon-plus {
	width: 1.5rem;
	height: 1.5rem;
	fill: $openFilterForegroundColor;
}

.filter-closeCard {
	position: fixed;
	left: 0;
	right: 0;
	bottom: 0;
	padding: $halfSpace 0 $halfSpace 0;
	background-color: $filterCloseCardBackgroundColor;
}

.filter-closeCard-content {
	display: flex;
	align-items: flex-end;
	flex-direction: column;
}


.filter-closeCardButton {
	border: none;
	background: $filterCloseCardButtonBackgroundColor;
	font-weight: bold;
}

.filterMobile-card {
	.acc {
		background: $filterMobileCardBackgroundColor;
	}

	.acc-openCloseIcon {
		fill: $openFilterForegroundColor;
	}
}


.filter-removeAllButton {
	@include hoverEtc {
		background: none;
		color: inherit;
	}
}

.filter-moreFilteringText {
	padding: 0.5rem;
	margin-right: 0.5rem;
	margin-bottom: 0.5rem;
}