.linkList {
	@include noBullets;
	margin-left: 0;
}

.linkList--grey {
	.interfaceLink {
		color: getColor(grijs darkest);
		&:hover, &:active, &:focus {
			color: getColor(hemelblauw default);
		}
	}
}

.linkList-item {
	break-inside: avoid;
}

.linkList-item--condensed {
	padding-top: 0;
	padding-bottom: 0;
}

.linkList-icon {
	flex: 0 0 auto;
	width: 1em;
	height: 1em;
	margin-top: 0 !important; /* stylelint-disable-line declaration-no-important */
	margin-right: 1em;
}

.linkList-title {
	margin-top: 0.5rem;
}

.linkList-legalMods,
.linkList-internal {
	border-top: 3px solid getColor(hemelblauw default);
	margin-top: 1.5rem;
}

.subjectLinkList {
	margin-top: 2rem;
	margin-bottom: 1.5rem;
	order: 4;

	&.contact {
		margin-top: 1rem;
		@include mqLesserThan($twoColumnGridBreakpoint) {
			margin-bottom: 0;
		}
	}
}

.subjectLinkList-header {
	margin-top: 0;

	.interfaceLink {
		color: getColor(hemelblauw darkest);
		border-bottom: none !important; /* stylelint-disable-line declaration-no-important */
		fill: getColor(hemelblauw darkest);

		@include hoverEtc {
			color: getColor(hemelblauw default);
			fill: getColor(hemelblauw default);
		}
	}
}
