/* LINKS */

// Add underline
@mixin underline {
	text-decoration: none;
	border-bottom: 1px solid;
}

// Undo underline
@mixin noUnderline {
	text-decoration: none;
	border-bottom: none;
}

// Hide underline without changing the dimensions relative
// to an underlined link
@mixin hideUnderline {
	text-decoration: none;
	border-bottom: 1px solid transparent;
}

a {
	color: inherit;
	@include underline;
	transition-property: color, fill;
	transition-duration: 400ms;

	&:hover,
	&:focus,
	&:active {
		color: $baseLinkHoverColor;
		fill: $baseLinkHoverColor;
	}
}

h2,
h3,
h4,
h5,
h6 {
	a {
		@include noUnderline;
		&:hover,
		&:focus,
		&:active {
			@include underline;
		}
	}
}

article a:not(.interfaceLink) {
	color: $articleLinkColor;
	fill: $articleLinkColor;

	&:hover,
	&:focus,
	&:active {
		color: $articleLinkHover;
		fill: $articleLinkHover;
	}
}

.interfaceLink {
	background: none;
	border: none;
	padding: 0;

	@include theme((color: foreground, fill: foreground), #{&}, true);

	@at-root {
		&,
		.interfaceLinkStatic {
			@include noUnderline;
			color: getColor(hemelblauw grijscontrast);
			fill: getColor(hemelblauw grijscontrast);
			@include lineHeight(root);

			display: inline-flex;
			align-items: flex-start;
		}
	}

	&--noFlex {
		display: inline-block;
	}

	&--block {
		display: block;
	}

	&--more {
		font-weight: bold;
	}

	&--white {
		color: getColor(wit);
		fill: getColor(wit);
	}

	&--asText {
		color: getColor(grijs darkest);
	}

	&--add {
		margin-top: 0.75rem;
		font-weight: bold;
	}
	&--remove {
		margin-bottom: 1.2rem;
		font-weight: bold;
	}

	&-icon {
		width: 1rem;
		flex: 0 0 auto;
		margin-right: 0.9em;

		@include theme((fill: foreground), #{&}, true);

		&--dark {
			fill: getColor(hemelblauw darkest);
		}
	}

	&-text {
		margin: 0;
		flex: 1 1 auto;
		color: inherit;
		line-height: inherit;
		@include fontSizeAlways(inherit);
	}

	.underliner {
		@include hideUnderline;
		transition: border-bottom-color 200ms;
	}

	@include hoverEtc {
		.underliner {
			@include underline;
		}
	}
}

.headingLink {
	// used to set icon size
	$lineHeight: getLineHeight(heading);

	width: 100%;
	@include lineHeight(heading);
	@include noUnderline;
	color: getColor(hemelblauw darkest);
	fill: getColor(hemelblauw darkest);
	display: inline-flex;
	align-items: center;
	position: relative;
	font-weight: bold;
	@include theme((fill: foregroundDarker), #{&}, true);

	.underliner {
		@include hideUnderline;
		transition: border-bottom-color 200ms;
	}

	@include hoverEtc {
		@include noUnderline;
		.underliner {
			@include underline;
		}
	}

	&-bigIcon {
		width: 2em;
		height: 2em;
		margin-right: 0.5em;
		flex: 0 0 auto;
		vertical-align: middle;
	}

	&-icon {
		flex: 0 0 auto;
		height: ($lineHeight * 1em);
		vertical-align: middle;
		margin-left: 0.3rem;
	}

	&-leadingIcon {
		flex: 0 0 auto;
		height: ($lineHeight * 1em);
		margin-right: 0.3em;
		vertical-align: middle;
	}

	&-text {
		flex: 0 1 auto;
		$lineHeight: getLineHeight(heading);
	}

	.goIcon {
		position: relative;
		top: -1px;
	}
}

.homeBlock-icon {
	@include theme((fill: foreground), '.homeBlockMore');
	width: 1.6em;
	height: 1.6em;
	margin-right: 10px;
}

.externalLinkIcon {
	height: 0.9rem;
	width: 0.9rem;
	display: inline-block;
	vertical-align: top;
}

.linkAnkeiler {
	color: getColor(grijs darkest);

	@include hoverEtc {
		color: inherit;
	}
}

.linkBox {
	margin-bottom: 15px;
	padding: 10px;
	border: 1px solid #d9d9d9;
	@include fontSizeAlways(1.1em);

	@include mqGreaterThan(extraLarge) {
		width: 50%;
	}

	a {
		width: 100%;
		color: getColor(zwart);
	}

	p {
		margin: 0;
	}

	.goIcon {
		float: right;
		fill: getColor(zwart);
	}
}

.blockLink,
.link {
	.underliner {
		@include hideUnderline;
	}
	.linkArrow {
		transition: transform 0.2s ease-in-out;
	}

	@include hoverEtc {
		border-color: inherit;

		.underliner {
			@include underline;
		}

		.linkArrow {
			transform: translateX(2px);
		}

		.hoverColor {
			color: inherit;
			fill: inherit;
		}

		.hoverIconBgColor {
			background-color: getColor(hemelblauw default);
			transition: background-color 0.25s ease-in-out;
		}
	}
}

.blockLink {
	display: block;
	border: none;
	@include hoverEtc {
		.headingEmphasized {
			color: getColor(hemelblauw darkest);
			fill: getColor(hemelblauw darkest);
		}

		.promoBlock-figure:after {
			width: 100%;
			left: 0;
		}
	}
}

.underlineLink {
	.underliner {
		@include hideUnderline;
	}

	@include hoverEtc {
		.underliner {
			@include underline;
		}
	}
}
