.themaLinks {
	li {
		margin-right: 0.5rem;
		margin-bottom: 0.5rem;
	}
}

.themaLinks-link {
	padding: 0.5rem 1rem;
	border: 0;
	background-color: $themaLinksBgColor;
	border-radius: 5px;
	transition: background-color ease-in-out 0.3s;

	@include hoverEtc {
		background-color: $themaLinksHoverBgColor;

		.themaLinks-text {
			color: $themaLinksHoverTextColor;
		}
	}
}

.themaLinks-text {
	color: $themaLinksTextColor;
	transition: color ease-in-out 0.3s;
}