.media {
	display: flex;
	flex-direction: row;
	align-items: center;

	&--alignTop {
		align-items: flex-start;
	}

	&--bordered {
		margin-bottom: 0.75rem;
		padding-top: 0.5rem;
		padding-bottom: 0.5rem;
		border-width: 1px;
		border-style: solid;
		border-left: 0;
		border-right: 0;
	}

	@include theme((border-color: foreground), '.media', true);
}

.inlineMedia {
	display: inline-flex;
	flex-direction: row;
	align-items: center;
}

.media-text {
	flex: 1 1 auto;
}

.media-figure {
	max-width: none;
	flex: 0 0 auto;
}