.blockContact {
	border-top: 3px solid getColor(paars default);
	padding-top: 0.5rem;
	padding-bottom: $space;

	&-header {
		padding-bottom: 0.5rem;
		border-bottom-width: 1px;
		border-bottom-style: solid;
	}

	&-title {
		margin-top: 0;
		margin-bottom: 0.25rem;
		padding-top: 0.5rem;
		color: getColor(paars default);
	}

	&-organisation {
		display: inline-block;
	}

	&-item {
		margin: 0;
		padding: 0.5rem 0;
		display: flex;
		align-items: center;
		border-bottom: 1px solid getColor(paars default);
		min-height: 3rem;
	}

	&-icon {
		width: 1.55rem;
		height: 1.55rem;
		fill: getColor(paars default);

		&--link,
		&--social {
			padding-left: 0.3rem;
		}

		+ .media-text {
			margin-left: 1rem;
		}
	}

	&-link {
		display: block;
	}

	.emphasis {
		color: getColor(paars default);
	}

	.phone-number {
		@include fontSizeAlways(1.2em);
		display: inline-block;
	}

	.externalLinkIcon {
		display: none;
	}
}
