/*------------------------------------*\
	$MEDIA ELEMENTS
\*------------------------------------*/

img {
	object-fit: cover;
}

/* Flexible Media */
img, video, object {
	display: block;
	max-width: 100%;
	height: auto;
}

svg {
	display: block;
	max-width: 100%;
}

iframe {
	border: none;
}

figure, .image-figure {
	margin: 0.8rem 0;
	img, .image {
		margin-bottom: $quarterSpace;
	}
}

figcaption, .image-caption {
	font-style: italic;
	@include fontSize(small);
}

.video-container {
	position: relative;
	padding-bottom: 56.25%;
	padding-top: 30px;
	height: 0;
	overflow: hidden;

	iframe,
	object,
	embed {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}

.imageLink {

	figure {
		margin: 0;
		img {
			transition: transform 0.2s;
			transform-origin: 50% 100%;
			margin-bottom: 1em;
			width: 100%;
		}
		@include hoverEtc {
			transform: scale(1.025);
		}
	}
}

.fullWidthImage {
	width: 100%;
}