.checkBox {
	display: flex;
	position: relative;
	margin: {
		right: 0.5rem;
		bottom: 0.5rem;
	}

	&-label {
		display: flex;
		cursor: pointer;
		padding-bottom: 0;
		background: getColor(hemelblauw lighter);
		transition: background 700ms;

		&.selected {
			@include theme((color: background), '.filters');
			@include theme((background: foreground), '.filters');
		}
	}

	&-input {
		@include hoverEtc {
			left: -999em;
		}

		&:focus + .checkBox-label {
			@include focusStyle;
		}
	}

	&-text {
		padding: 0.5rem;
	}

	.close {
		//@include theme()
		@include theme((background: foreground), '.filters');
		width: 2.6rem;
		height: 2.6rem;

		&-icon {
			fill: white;
		}
	}
}