.tabs {
	@include noBullets;
	position: relative;
	@include lineHeight(heading);
	@include zIndex(tabs);
	@include fontSize(small);

	@include mqGreaterThan($twoColumnGridBreakpoint) {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: flex-end;
	}
}

.tab {
	padding: 0;
	& + & {
		@include mqGreaterThan($twoColumnGridBreakpoint) {
			margin-left: 0.35rem;
		}
	}

	&-button {
		@include cleanButton;
		display: block;
		width: 100%;
	}

	&-link {
		@include noUnderline;
		display: block;
		padding: 0.6rem 0.8rem;
		color: getColor(wit);
		fill: getColor(wit);
		position: relative;
		@include zIndex(tabs normal);

		@include before('') {
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			background-color: getColor(hemelblauw darkest);
			transition: top 0.1s ease-in-out, background-color 0.1s ease-in-out;

			@include mqGreaterThan($twoColumnGridBreakpoint) {
				background-image: linear-gradient(to top,
					#{rgba(getColor(zwart), 0.15)} 0.25rem,
					transparent 0.25rem);
			}
		}

		@include mqGreaterThan($twoColumnGridBreakpoint) {
			transform-origin: 50% 100%;
		}

		@include hoverEtc {
			@include mqLesserThan($twoColumnGridBreakpoint) {
				color: getColor(wit);
				fill: getColor(wit);
			}
			@include mqGreaterThan($twoColumnGridBreakpoint) {
				color: getColor(wit);
				fill: getColor(wit);
			}

			@include before() {
				@include mqLesserThan($twoColumnGridBreakpoint) {
					background-color: getColor(hemelblauw default);
				}

				@include mqGreaterThan($twoColumnGridBreakpoint) {
					top: -0.15rem;
					background-image: linear-gradient(to top,
						#{rgba(getColor(zwart), 0.15)},
						#{rgba(getColor(zwart), 0.15)});
				}
			}
		}

		&--small {
			@include mqLesserThan($twoColumnGridBreakpoint) {
				display: block;
			}
			@include mqGreaterThan($twoColumnGridBreakpoint) {
				display: none;
			}
		}

		&--large {
			@include mqLesserThan($twoColumnGridBreakpoint) {
				display: none;
			}
			@include mqGreaterThan($twoColumnGridBreakpoint) {
				display: block;
			}
		}

		&--dropdown {
			width: 100%;
			margin-top: 2px;
			background-color: getColor(hemelblauw lighter);
			border: 0;
			color: getColor(hemelblauw darkest);
			fill: getColor(hemelblauw darkest);
			text-align: left;

			@include mqGreaterThan(small) {
				padding: 0.6rem ($space + 0.8rem);
			}
			@include hoverEtc() {
				color: getColor(hemelblauw darkest);
				fill: getColor(hemelblauw darkest);
			}

			@include before() {
				display: none;
			}
		}

		&.is-active {
			@include zIndex(tabs active);
			font-weight: bold;
			@include mqLesserThan($twoColumnGridBreakpoint) {
				box-shadow: 0 -4px 0 0 rgba(getColor(zwart), 0.15), 0 4px 0 0 rgba(getColor(zwart), 0.15);

				@include before() {
					left: -0.25rem;
					right: -0.25rem;
				}

				&.tab-link--dropdown {
					box-shadow: none;
				}
			}

			@include mqGreaterThan($twoColumnGridBreakpoint) {
				color: getColor(hemelblauw darkest);
				fill: getColor(hemelblauw darkest);

				@include before() {
					top: -0.25rem;
					background-color: #{getColor(wit)};
					background-image: none;
				}
			}
		}

		&--content.is-active {
			@include mqGreaterThan($twoColumnGridBreakpoint) {
				@include before() {
					background-color: #{getColor(grijs lightest)};
				}
			}
		}

		&--openClose {
			&,
			&.is-active {
				box-shadow: none;
			}
		}
	}

	&-icon {
		position: relative;
		width: 1.3em;
		height: 1.3em;
		margin-top: 0;
		margin-right: 0.5em;
	}

	&-text {
		position: relative;
	}

	&-openClose {
		position: relative;
		@include before('') {
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			background-color: getColor(grijs lightest);
			box-shadow: 0 0 0 1px getColor(grijs default) inset;
		}
		&.is-active {
			@include zIndex(tabs active);
			box-shadow: 0 -4px 0 0 rgba(getColor(zwart), 0.15), 0 4px 0 0 rgba(getColor(zwart), 0.15);
			@include before('') {
				@include mqLesserThan($twoColumnGridBreakpoint) {
					left: -0.25rem;
					right: -0.25rem;
				}
			}
		}

		&-link {
			&.is-opened {
				color: getColor(hemelblauw darkest);
				fill: getColor(hemelblauw darkest);

				@include before() {
					top: -0.25rem;
					background-color: transparent;
					background-image: none;
				}
			}
		}
		&-content {
			position: relative;
			overflow: hidden;
		}
		&-icon {
			position: relative;
			width: 1.8em;
			height: 1.8em;
			margin: -0.3em 0 -0.3em 0.3em;


			transition: transform 400ms;
			transform: rotate(0deg);

			&.is-opened {
				transform: rotate(-180deg);
			}
		}
	}
}

.dropdownContainer {
	.tab-link {
		color: getColor(hemelblauw default);
		fill: getColor(hemelblauw default);

		@include mqGreaterThan(small) {
			padding: 0.6rem ($space + 0.8rem);
		}

		&:before {
			background-color: getColor(hemelblauw lightest);
		}
	}
}