.themedBackground {
	@include theme((background: background), #{&}, true);
}

.themedDarkBackground {
	@include theme((background: foreground), #{&}, true);
}

.themedSideBackground {
	@include theme((background: background), #{&}, true);
	position: relative;

	@include before('') {
		position: absolute;
		top: 0;
		bottom: 0;
		right: 100%;

		width: 9999px;
		width: 100vw;
	}
	@include beforeTheme((background: background), #{&}, true);
}