.tool {
	&-markup {
		ul {
			@include noBullets;

			li {
				display: flex;

				svg {
					width: 1.2rem;
					height: 1.6rem;
					margin-right: 0.5rem;
					fill: getColor(hemelblauw default);
				}
			}
		}
	}

	&-content {
		background-color: getColor(hemelblauw lightest);
		border-top: 3px solid getColor(hemelblauw default);
		padding-left: 1rem;
		padding-top: 1rem;
		padding-bottom: 0.5rem;

		h3 {
			margin: 0;
		}

	}

	&-block {
		margin-top: 1rem;
		margin-bottom: 1rem;
	}

	&-link {
		margin-left: 0;
		border-bottom: none;
		@include hoverEtc {
			.tool-content {
				background-color: getColor(hemelblauw lighter);
				transition: background-color 400ms;
			}
		}
	}

	&-title {
		color: getColor(hemelblauw default);
	}
}