$accBorderTopColor: inherit !default;
$accOpenCloseIconColor: getColor(hemelblauw default) !default;
$accContentHeadingFontWeight: inherit !default;
$accTitlesColor: getColor(hemelblauw default) !default;
$accTitlesHoverColor: getColor(hemelblauw darkest) !default;
$accTitlesActiveColor: inherit !default;
$accTitlesActiveHoverColor: inherit !default;

.accordion {
	margin-bottom: 2rem;
}

.accordion-toggles {
	text-align: right;
	@include lineHeight(heading);
	padding-bottom: 1rem;
}

.accordion-toggle {
	&:last-child {
		margin-left: 0.5rem;
	}
}

.accordion-toggle--button {
	@include cleanButton;
	@include underline;
	color: getColor(hemelblauw default);
	transition: border-bottom-color 0.3s ease-in-out;

	@include hoverEtc {
		border-bottom-color: transparent;
		color: getColor(hemelblauw grijscontrast);
		fill: getColor(hemelblauw grijscontrast);
	}
}

.accordion-toggle--inactive {
	color: getColor(grijs grijs7);
}

.acc {
	@include theme((border-top-color: foreground), '.accordion');
	border-top: 1px solid;
	border-top-color: $accBorderTopColor;

	&.isOpened {
		background-color: white;

		@include theme((border-top-color: foreground), '.accordion');
	}
}

.acc-openCloseIcon {
	cursor: pointer;
	transition: transform 0.3s;
	fill: $accOpenCloseIconColor;

	svg {
		height: 1rem;
		width: 1rem;
	}

	.isOpened & {
		transform: rotate(135deg);
		fill: getColor(hemelblauw grijscontrast);
	}
}

.acc-openCloseAll {
	display: block;
	margin-bottom: 1rem;
	margin-left: auto;
}

.acc-openCloseAll-button {
	display: flex;
	align-items: center;
	fill: getColor(hemelblauw default);
	@include cleanButton;

	svg {
		width: 1.5rem;
		height: 1.5rem;
		fill: getColor(hemelblauw default);
	}

	@include hoverEtc {
		color: getColor(hemelblauw darkest);
		fill: getColor(hemelblauw darkest);
	}
}

.acc-openCloseAll-button-text {
	margin-right: 10px;
}

.acc-header {
	margin-top: 1rem;
	margin-bottom: 1rem;
	fill: getColor(hemelblauw default);
	
	@include hoverEtc {
		color: getColor(hemelblauw darkest);
		fill: getColor(hemelblauw darkest);
	}
}

.acc-titles {
	margin-top: 1rem;
	margin-bottom: 1rem;
	color: $accTitlesColor;
	flex: 1 1 0;

	@include hoverEtc {
		color: $accTitlesHoverColor;

		+ .acc-openCloseIcon {
			fill: getColor(hemelblauw darkest);
		}
	}

	.isOpened & {
		color: $accTitlesActiveColor;
		font-weight: bold;

		@include hoverEtc {
			color: $accTitlesActiveHoverColor;
		}
	}
}

.acc-titles-text {
	border-bottom: 1px solid transparent;
	transition: border-bottom-color 0.3s ease-in-out;
}

.acc-title {
	margin-right: 0.5rem;
	margin-top: 0;
	margin-bottom: 0;

	.isOpened & {
		@include theme((color: foreground), '.accordion');
	}
}

.acc-content-body {
	padding-top: 0.5rem;
	padding-bottom: 1rem;

	// Make sure all kind of different half column blocks in accordions
	// are aligned correctly
	.column--half {
		margin-top: 1rem;
		margin-bottom: 1rem;
	}
	.directRegelen-list {
		width: 100%;
	}
}

.acc-content-heading {
	margin: 0;
	font-weight: normal;
}

.acc-content-title {
	@include cleanButton;
	width: 100%;
	display: block;

	@include hoverEtc {
		@include theme((background: background), '.accordion');

		.acc-titles-text {
			border-bottom-color: inherit;
			color: $accTitlesHoverColor;
		}
	}
}

.acc-smoothOpen {
	padding-top: $quarterSpace;
	padding-bottom: $halfSpace;
}