$defaultThemes: (
	blue: (
		foregroundDarker: getColor(hemelblauw grijscontrast),
		foregroundLighter: getColor(hemelblauw light),
		foreground: getColor(hemelblauw default),
		background: getColor(hemelblauw lighter),
		backgroundTransparent: rgba(getColor(hemelblauw lightest), 0.9),
	),
	aqua: (
		foregroundDarker: getColor(aqua default),
		foregroundLighter: getColor(aqua default),
		foreground: getColor(aqua default),
		background: getColor(aqua default),
		backgroundTransparent: rgba(getColor(aqua default), 0.9),
	),
	grey: (
		foregroundDarker: getColor(grijs default),
		foregroundLighter: getColor(grijs light),
		foreground: getColor(grijs default),
		background: getColor(grijs lightest),
		backgroundTransparent: rgba(getColor(grijs lightest), 0.9),
	),
	purple: (
		foregroundDarker: getColor(paars darker),
		foregroundLighter: getColor(paars light),
		foreground: getColor(paars default),
		background: getColor(paars lightest),
		backgroundTransparent: rgba(getColor(paars lightest), 0.9),
	),
	green: (
		foregroundDarker: getColor(mosgroen darker),
		foregroundLighter: getColor(mosgroen light),
		foreground: getColor(mosgroen default),
		background: getColor(mosgroen lightest),
		backgroundTransparent: rgba(getColor(mosgroen lightest), 0.9),
	),
	violet: (
		foregroundDarker: getColor(violet default),
		foregroundLighter: getColor(violet light),
		foreground: getColor(violet default),
		background: getColor(violet lightest),
		backgroundTransparent: rgba(getColor(violet lightest), 0.9),
	),
	orange: (
		foregroundDarker: getColor(oranje alternative),
		foregroundLighter: getColor(oranje alternative),
		foreground: getColor(oranje alternative),
		background: getColor(oranje alternative),
		backgroundTransparent: rgba(getColor(oranje alternative), 0.9),
	),
);

@mixin themeProperties($propertyMap, $colors, $themes: $defaultThemes) {
	@each $propertyName, $colorType in $propertyMap {
		#{$propertyName}: mapDeepGet($colors, $colorType);
	}
}

@mixin theme($propertyMap, $baseClass: 'theme', $sameBlock: false, $themes: $defaultThemes) {
	@each $name, $colors in $themes {
		@if ($sameBlock) {
			&#{$baseClass}--#{$name} {
				@include themeProperties($propertyMap, $colors, $themes);
			}
		} @else {
			#{$baseClass}--#{$name} & {
				@include themeProperties($propertyMap, $colors, $themes);
			}
		}
	}
}

@mixin beforeTheme($propertyMap, $baseClass: 'theme', $sameBlock: false, $themes: $defaultThemes) {
	@each $name, $colors in $themes {
		@if ($sameBlock) {
			&#{$baseClass}--#{$name}:before {
				@include themeProperties($propertyMap, $colors, $themes);
			}
		} @else {
			#{$baseClass}--#{$name} &:before {
				@include themeProperties($propertyMap, $colors, $themes);
			}
		}
	}
}


@mixin afterTheme($propertyMap, $baseClass: 'theme', $sameBlock: false, $themes: $defaultThemes) {
	@each $name, $colors in $themes {
		@if ($sameBlock) {
			&#{$baseClass}--#{$name}:after {
				@include themeProperties($propertyMap, $colors, $themes);
			}
		} @else {
			#{$baseClass}--#{$name} &:after {
				@include themeProperties($propertyMap, $colors, $themes);
			}
		}
	}
}

@include theme((color: foreground), '.theme', true);