$socialFigureFooterForegroundColor: #baddef !default;
$socialFigureBackgroundColor: getColor(hemelblauw darkest) !default;
$socialFigureForegroundColor: getColor(wit) !default;

.social {
	&-link {
		display: block;
		height: 2rem;
		width: 2rem;
		@include noUnderline;
		margin: 0 0.5rem;
		transition: all 0.4s ease-in-out;

		@include hoverEtc {
			transform: scale(1.1);
		}

		&--facebook {
			@include hoverEtc {
				.social-figure-background {
					fill: getColor(socials facebook);
				}
			}
		}
		&--twitter {
			@include hoverEtc {
				.social-figure-background {
					fill: getColor(socials twitter);
				}
			}
		}
		&--linkedin {
			@include hoverEtc {
				.social-figure-background {
					fill: getColor(socials linkedin);
				}
			}
		}
		&--googleplus {
			@include hoverEtc {
				.social-figure-background {
					fill: getColor(socials googleplus);
				}
			}
		}
		&--youtube {
			@include hoverEtc {
				.social-figure-background {
					fill: getColor(socials youtube);
				}
			}
		}
		&--email,
		&--email-2012 {
			@include hoverEtc {
				.social-figure-background {
					fill: getColor(hemelblauw default);
				}
			}
		}


		@include hoverEtc {
			.social-figure-foreground {
				fill: getColor(wit);
			}
		}

		.social--share & {
			@include hoverEtc {
				.social-figure-background {
					fill: getColor(wit);
				}
				.social-figure-foreground {
					fill: getColor(hemelblauw default);
				}
			}
		}
	}

	&-figure {
		height: 2rem;
		width: 2rem;
		max-width: none;
		display: block;
		position: relative;


		&-background,
		&-foreground {
			transition: fill 400ms;
		}

		&-background {
			fill: $socialFigureBackgroundColor;
			@include theme((fill: foreground), #{&}, true);
		}

		&-foreground {
			width: 1.2rem;
			height: 1.2rem;
			position: absolute;
			top: 0.4rem;
			left: 0.4rem;
			fill: $socialFigureForegroundColor;

			.footer & {
				fill: $socialFigureFooterForegroundColor;
			}
		}

		.social--share & {

			&-background {
				fill: getColor(hemelblauw lighter);
			}

			&-foreground {
				fill: getColor(hemelblauw default);

				.footer & {
					fill: #baddef;
				}
			}
		}
	}

	&--share {
		margin-left: 0;
		padding-right: 1.8rem;

		@include mqGreaterThan(small) {
			padding-right: 2.7rem;
		}
	}

	&--follow {
		margin: $pad-half 0 $pad;
	}
}