.region {
	&-search {
		background-color: getColor(mosgroen light);
		@include theme((background-color: backgroundTransparent), #{&}, true);

		&-intro {
			margin-top: 1rem;
			margin-bottom: 1rem;
		}

		&-box {
			display: flex;
			align-items: center;
			margin-top: 1rem;
			padding-bottom: 0.75rem;
			@include mqLesserThan(smallMedium) {
				display: block;
			}

			&-input {
				@include setPropWithMq(greater, width, (
						all: 100%,
						smallMedium: 66%,
						mediumLarge: 50%
					)
				);
			}

			&-title {
				margin: 0;
				margin-right: 1rem;
				padding-bottom: 0;
				font-weight: bold;
				color: getColor(hemelblauw darkest);
			}
		}
	}

	&-list {
		@include mqGreaterThan(smallMedium) {
			column-count: 2;
		}

		@include mqGreaterThan(mediumLarge) {
			column-count: 3;
		}

		&-group {
			padding-top: 1rem;
			border-top-width: 3px;
			border-top-style: solid;
			@include theme((border-top-color: foregroundDarker), #{&}, true);
		}

		&-title {
			margin: 0;
			margin-bottom: 0;
			color: getColor(hemelblauw default);
			@include theme((color: foregroundDarker), #{&}, true);
		}

		&-item {
			@include breakInsideAvoid(inline-flex);
			margin-bottom: 1.5rem;

			button,
			.headingLink {
				@include cleanButton;
				align-items: flex-start;
				font-weight: bold;
				width: auto;

				@include hoverEtc {
					color: getColor(hemelblauw default);
					fill: getColor(hemelblauw default);
				}

				.goIcon:first-child {
					margin-right: 1rem;
				}
			}
		}
	}

	&-backButton {
		@include cleanButton;
		margin-bottom: 1.5rem;

		.goIcon {
			margin-right: 0.5rem;
		}
	}

	&-logo {
		margin-right: $halfSpace;
		margin-top: 0.18rem;
		width: 5rem;
		height: 5rem;
		padding: $quarterSpace;
		border: 1px solid getColor(groen default);

		&-image {
			@supports(object-fit: contain) {
				object-fit: contain;
				height: 100%;
				width: 100%;
			}
		}
	}

	&-contact-block {
		@include mqLesserThan(smallMedium) {
			margin-top: 1rem;
		}
	}
}