.alphabet-list {
	position: relative;
	left: -0.42em;
	color: getColor(hemelblauw darkest);
	font-weight: bold;

	@include mqGreaterThan(extraLarge) {
		left: -0.75em;
	}

	@include mqGreaterThan(extraExtraLarge) {
		left: -0.75em;
	}

	&-item {
		padding: 0.65em 1.1em;
		display: inline-block;
		border: 0;
		text-decoration: none;

		.underliner {
			border-bottom-color: transparent;
			transition: border-bottom-color 0.3s ease-in-out;
		}

		@include mqGreaterThan(extraLarge) {
			padding: 0.62em;
		}

		@include mqGreaterThan(extraExtraLarge) {
			padding: 0.8em;
		}

		@include hoverEtc {
			.underliner {
				border-bottom: 2px solid getColor(hemelblauw darkest);
				@include theme((color: foreground, border-bottom-color: foreground), '.alphabet-list');
			}
		}

		&--selected {
			background-color: getColor(wit);

			.underliner {
				border-bottom-color: transparent !important; /* stylelint-disable-line declaration-no-important */
			}
		}

		&.inactive {
			opacity: 0.25;
		}
	}
}