.cardContainer {
	@include mqGreaterThan($cardBreakpoint) {
		position: fixed;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		@include zIndex(overlay);
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
	}

	&--inBg {
		@include mqLesserThan($cardBreakpoint) {
			position: absolute;
		}
	}
}

.card {
	position: relative;

	@include insideMarginBottom($halfSpace);
	@include mqGreaterThan($cardBreakpoint) {
		max-width: 30rem;
		max-height: 90vh;
		margin: 0 auto;

		&--wide {
			max-width: $maxWidth;
		}
	}

	&-content {
		background-color: getColor(wit);
		@include mqGreaterThan($cardBreakpoint) {
			display: flex;
			flex-direction: column;
			max-height: 90vh;
			overflow-y: auto;
		}
	}

	&-title {
		color: getColor(wit);
		margin: 0;
	}

	&-subTitle {
		color: getColor(wit);
		margin: 0;
		font-weight: normal;
	}
}





.backdropOverlay {
	@include cleanButton;
	@include hideText;
	position: fixed;
	background-color: rgba(getColor(hemelblauw darkest), 0.6);
	width: 100%;
	cursor: default;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;

	&--bigOnly {
		@include mqLesserThan($cardBreakpoint) {
			display: none;
		}
	}
}