/* BLOCKS */

.blockContainer {
	margin: {
		top: $halfSpace;
		bottom: $halfSpace;
	}
	@include theme((fill: foreground), '.blockContainer', true);

	&-more {
		$baseThemeClass: #{&};
		@include lineHeight(textSize);
		flex: 0 0 auto;
		padding: $halfSpace $halfSpace 0.45rem 0;
		position: relative;
		@include before('') {
			display: block;
			border-top: 2px solid;
			height: 0.5rem;
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
		}
		@include beforeTheme((border-top-color: foreground), $baseThemeClass, true);

		.interfaceLink,
		.interfaceLinkStatic {
			color: getColor(grijs darkest);
		}

		@at-root {
			.interfaceLink,
			.interfaceLinkStatic {
				@include theme((fill: foreground), $baseThemeClass);

				@include hoverEtc {
					@include theme((fill: foreground, color: foreground), $baseThemeClass);
				}
			}
		}

	}

	&--stretchItems {
		display: flex;
		align-items: stretch;
	}
}

.homeBlockMore {
	$baseThemeClass: '.homeBlockMore';
	flex: 0 0 auto;
	padding: 0.75rem;
	padding-left: 0;
	border-top: 3px solid;
	@include theme((color: foreground, border-top-color: foreground), $baseThemeClass, true);

	.interfaceLink,
	.blockLink {
		color: inherit;
		font-weight: bold;
		margin-bottom: -0.55rem;

		.media {
			display: inline-flex;
		}
	}
}

%block {
	display: flex;
	flex-direction: column;
	position: relative;
}

.bigBlock {
	min-width: 100%;
	margin-top: 1.5rem;
}

.bigBlocks { //a.k.a. bigMac
	@include insideMargin($halfSpace);
	//border-top: 3px solid getColor(mosgroen default);
	@include theme((border-top-color: foreground), '.listBlock',true);
	border-top: 3px solid;

	.tab-icon {
		@include fontSizeAlways(1.2em);
	}
	.media-text {
		font-weight: bold;
	}

	.interfaceLink {

		color: getColor(zwart);
		&:hover {
			@include theme((color: foreground), '.listBlock');
		}
	}

	&-more {
		padding-left: 1rem;
		margin-bottom: 1rem;
		font-weight: bold;
		@include lineHeight(bigBlockMore);
		.interfaceLink-text {
			display: inline-table;
			margin-right: 5px;
			@include theme((color: foreground), '.listBlock',true);
		}
		.moreIcon {
			@include theme((fill: foreground), '.listBlock',true);
			margin-top: 2px;
		}
	}
	&-situation {
		margin-right: 10px;
		width: 25%;
		~ .interfaceLink-text {
			font-weight: bold;
		}
	}
}

.moreIcon {
	height: 1.4rem;
	margin-right: 1em;
	@include theme((fill: foreground), '.blockContainer');
	@include theme((fill: foreground), '.listBlock');
	@include theme((fill: foreground), '.homeBlock');
}

.threadDate {
	@include theme((color: foreground), '.bigBlocks.listBlock');
	@include lineHeight(root);
}

.relatedDetailPagesBlock {
	margin-top: 2.5rem;
	background-color: getColor(hemelblauw lightest);
	border-top: 3px solid getColor(hemelblauw default);
	border-bottom: 1px solid getColor(hemelblauw default);
	color: getColor(hemelblauw darkest);
	.block-title {
		color: getColor(hemelblauw default);
		margin-top: 1rem;
	}
	p {
		font-weight: bold;
	}
	.subjectListTitle {
		min-height: 3rem;
		font-weight: bold;
	}
}

.listBlock {
	$baseThemeClass: '.listBlock';

	@extend %block;
	//@include insideMargin($halfSpace);
	padding: 0 $halfSpace;
	padding-left: 0;


	&--autoHeight {
		min-height: 0;
	}

	&-title {
		font-weight: normal;
		margin-top: 0;

		@include theme((color: foreground), $baseThemeClass);
	}

	&-icon {
		height: 1.35rem;
		width: 1.35rem;
		margin-right: 0.5rem;

		@include theme((fill: foreground), $baseThemeClass);
	}

	&-list {
		display: flex;
		flex-flow: column wrap;

		&,
		.interfaceLink {
			@include lineHeight(heading);
		}
		.goIcon {
			margin: {
				top: -0.15rem;
				bottom: -0.35rem;
			}
		}
	}


	.interfaceLink {
		color: getColor(grijs darkest);
	}

	// At root is used to remove extranious .listBlock class
	@at-root {
		.interfaceLink {
			@include theme((fill: foreground), $baseThemeClass);

			@include hoverEtc {
				@include theme((fill: foreground, color: foreground), $baseThemeClass);
			}
		}
	}
}

.homeBlock {
	$baseThemeClass: '.homeBlock';
	cursor: pointer;
	//@include insideMargin($halfSpace);
	@include noUnderline;
	@extend %block;
	transition: color 0.2s;

	@include hoverEtc {
		@include theme((color: foreground), $baseThemeClass, true);

		.homeBlock-title {
			color: inherit;
		}
	}

	&--autoHeight {
		min-height: 0;
	}

	&-figure {
		margin: 0;
		position: relative;
		margin-bottom: 1rem;
	}

	&-image {
		width: 100%;
		margin: 0;
	}

	&-content {
		@include insideMargin($halfSpace);
		padding: 0 $halfSpace;
		padding-left: 0;

	}

	&-title {
		margin-top: 0;
		color: getColor(grijs darkest);
	}

}

.promoBlock {
	$baseThemeClass: '.promoBlock';
	@include theme((border-top-color: foreground), $baseThemeClass, true);
	border-top: 3px solid;
	@include noUnderline;
	@extend %block;
	transition: color 0.2s;
	transform-origin: 50% 100%;


	@include hoverEtc {
		@include theme((color: foreground), $baseThemeClass, true);
		.webinar & {
			color: getColor(hemelblauw default);
		}
		.promoBlock-title {
			color: inherit;
		}

		.promoBlock-figure:after {
			width: 100%;
			left: 0;
		}
	}

	&--autoHeight {
		min-height: 0;
	}

	&-top {
		position: relative;
	}

	&-figure {
		margin: 0;
		position: relative;

		&:after {
			position: absolute;
			width: 0;
			height: 100%;
			background: rgba(getColor(hemelblauw default),0.4);
			content: '';
			display: block;
			bottom: 0;
			left: 50%;
			transition: width 0.2s ease-in-out, left 0.2s ease-in-out;
		}

		.interfaceLink {
			display: block;
		}

		.promoBlock--purple & {
			&:after {
				background: rgba(getColor(paars default),0.4);
			}
		}

		.promoBlock--violet & {
			&:after {
				background: rgba(getColor(violet default),0.4);
			}
		}
	}

	&-link {
		display: block;
	}

	&-image {
		width: 100%;
		margin: 0;
		object-fit: cover;
		object-position: top;
		transition: transform 0.2s;

		&.externalImage {
			max-height: 10rem;
		}
	}

	&-content {
		width: 100%;
		display: inline; // so the external link icon will position correctly
		
		.infoText {
			margin-bottom: 0.5rem;
		}

		.block-title,
		.promoBlock-title {
			margin-top: 1rem;
		}

		a {
			border-bottom: 0;
		}

	}

	&-title {
		margin-top: 0;
		color: getColor(grijs darkest);

		.metaInfo {
			display: inline-block;
			margin-left: 1.25rem;
			font-weight: normal;
			white-space: nowrap;
		}
	}


	&-label {
		position: absolute;
		bottom: 0;
		left: -0.05em;
		padding: 0.25em 0.75em;
		@include fontSize(small);
		color: getColor(wit);
		background-color: getColor(hemelblauw default);
		@include theme((background-color: foreground), $baseThemeClass);
	}

	&-go {
		order: 2;
		flex: 1 0 auto;
		background-color: getColor(hemelblauw default);
		@include theme((background-color: foreground), $baseThemeClass);
		@include insideMargin($quarterSpace);
		padding: 0 $quarterSpace;

		&-title {
			font-weight: normal;
			display: flex;
			margin: 0;
			color: getColor(wit);
			fill: getColor(wit);

			.goIcon {
				margin: {
					top: -0.25rem;
					bottom: -0.25rem;
				}
			}
		}
	}

	&-more {
		@include theme((color: foreground), '.blockContainer-more');
		font-weight: bold;
	}

	+ .blockContainer-more {
		padding-top: 0;

		&:before {
			display: none;
		}
	}

	&--story {
		border-top: 0;
	}
}

@media (pointer: coarse) and (hover: none) {
	.promoBlock-figure:after {
		content: none; // Prevent appearing pseudo content on hover for touch devices
	}
}

.block {
	margin: {
		top: $halfSpace;
	}
	@include insideMargin($halfSpace);
	padding: 0 $halfSpace;

	@include theme((background-color: background), #{&}, true);

	// Right Column
	&--side {
		border-top: 3px solid getColor(hemelblauw default);
		padding-top: 0.5rem;
		padding-bottom: 1.5rem;
		margin-bottom: 0;
		//border-bottom: 3px solid getColor(grijs border);
		padding-left: 0;

		&:last-child {
			border-bottom: none;
		}
	}

	&--grey {
		background-color: getColor(grijs lightest);
	}

	&--blue {
		background-color: getColor(hemelblauw lighter);

		.interfaceLink {
			color: getColor(hemelblauw darkest);
			fill: getColor(hemelblauw darkest);
		}
	}

}

.postTitle {
	display: block;
}

.postDate {
	font-weight: normal;
	font-style: italic;
	@include fontSize(extrasmall);
}


.pageBlockCallToAction {
	font-weight: bold;
	@include theme((color: foreground), '.blockContainer');
	margin-bottom: $quarterSpace;
}

.pageBlockAnkeiler {
	@include hoverEtc {	
		@include theme((color: foreground), '.blockContainer');
	}
}

.pageBlockAnkeilerTitle {
	@include theme((color: foreground), '.blockContainer');
}

.pageBlockMore {
	border-top: 1px solid;
	@include theme((border-color: foreground), '.blockContainer');
	padding-top: $quarterSpace;
}

.pageBlockMoreLink {
	display: flex;
	font-weight: normal;
	@include theme((color: foreground), '.blockContainer');

	.interfaceLink-text {
		flex: none;
	}
}